.companions {
  box-sizing: border-box;
  position: relative;
  z-index: 1;

  border-bottom-left-radius: 30px;
  border-bottom-right-radius: 30px;

  background: url("../img/smolov-back-desktop@1x.png"), url("../img/rogovaya-back-mobile@1x.png");
  background-color: @dark-bg;
  background-repeat: no-repeat;
  background-size: 143px 143px, 71px 71px;
  background-position: 85.5% 60px, -10px 332px;

  @media (min-resolution: @retina-dppx), (min-resolution: @retina-dpi) {
    background: url("../img/smolov-back-desktop@2x.png"), url("../img/rogovaya-back-mobile@2x.png");
    background-color: @dark-bg;
    background-repeat: no-repeat;
    background-size: 143px 143px, 71px 71px;
    background-position: 85.5% 60px, -10px 332px;
  }

  &::after {
    content: "";
    display: block;
    position: absolute;
    left: -18px;
    top: 59px;
    z-index: -1;
    width: 132px;
    height: 132px;

    background-color: @decoration-dark-bg;
    border-radius: 20px;
  }
}

.companions__container {
  margin-bottom: 56px;
}

.companions__wrapper {
  box-sizing: border-box;
  width: 84%;
  margin-left: auto;
  margin-right: auto;
  padding-top: 92px;
  padding-bottom: 30px;
}

.companions__title {
  .indents-reset;
  width: 53%;
  margin-left: 9%;
  margin-bottom: 20px;

  .title-font;
  color: @index-section-title;
}

.companions__paragraph {
  .indents-reset;
  width: 80%;
  margin-left: 9%;
  margin-bottom: 21px;

  color: @clear-font;

  word-wrap: break-word;

  br {
    display: none;
  }
}

.companions__condition {
  .indents-reset;
  width: 72%;
  margin-left: 9%;

  .bold-font;
  color: @clear-font;

  word-wrap: break-word;
}

.companions__list {
  margin-left: auto;
  margin-right: auto;
  margin-bottom: 20px;
}

.companions__to-all {
  box-sizing: border-box;
  display: block;
  width: 270px;
  margin-left: auto;
  margin-right: auto;
  padding-top: 17px;
  padding-bottom: 15px;
  padding-left: 23px;
  padding-right: 1px;

  .bold-font;
  font-size: 17px;
  line-height: 30px;
  letter-spacing: 0.4px;
  color: @font-yellow-button;

  background-color: @yellow-button-color;
  border-radius: 10px;

  .companions__to-all-icon {
    margin-left: 39%;
    fill: @font-yellow-button;
  }

  .button-actions;
}

@media (min-width: @tablet-width) {
  .companions {
    border-bottom-left-radius: 60px;
    border-bottom-right-radius: 60px;

    background: url("../img/smolov-back-desktop@1x.png"), url("../img/rogovaya-back-desktop@1x.png");
    background-color: @dark-bg;
    background-repeat: no-repeat;
    background-size: 143px 143px, 285px 285px;
    background-position: 6.2% 486px, 84.2% 140px;

    @media (min-resolution: @retina-dppx), (min-resolution: @retina-dpi) {
      ackground: url("../img/smolov-back-desktop@2x.png"), url("../img/rogovaya-back-desktop@2x.png");
      background-color: @dark-bg;
      background-repeat: no-repeat;
      background-size: 143px 143px, 285px 285px;
      background-position: 6.2% 486px, 84.2% 140px;
    }

    &::after {
      left: 5.1%;
      top: 140px;
      width: 213px;
      height: 213px;

      background-color: @decoration-dark-bg;
      border-radius: 20px;
    }
  }

  .companions__wrapper {
    width: 89%;
    padding-top: 200px;
    padding-bottom: 88px;
  }

  .companions__title {
    margin-bottom: 56px;
    margin-left: 4.6%;

    font-size: 45px;
    line-height: 45px;
  }

  .companions__paragraph {
    width: 70%;
    margin-bottom: 40px;
    margin-left: 5%;

    font-size: 28px;
    line-height: 33px;

    br {
      display: block;
    }
  }

  .companions__condition {
    width: 40%;
    margin-bottom: 58px;
    margin-left: 4.8%;

    font-size: 21px;
    line-height: 25px;

    span {
      display: block;
    }
  }

  .companions__to-all {
    box-sizing: border-box;
    width: 90.2%;
    margin-left: 4.9%;
    padding-top: 29px;
    padding-bottom: 27px;
    padding-left: 4.6%;

    font-size: 24px;
    line-height: 24px;
    letter-spacing: 0;

    border-radius: 20px;

    .companions__to-all-icon {
      margin-left: 65%;
    }
  }

  .companions__list {
    margin-bottom: 70px;
  }
}

@media (min-width: @desktop-width) {
  .companions {
    block-size: 143px 143px, 285px 285px;
    background-position: 547px 194px, 194px 446px;

    &::after {
      left: 60px;
      top: 129px;
    }
  }

  .companions__wrapper {
    width: 92%;
    max-width: 1462px;
    padding-top: 189px;
    padding-bottom: 93px;
    padding-left: 5%;
    padding-right: 0;
    display: flex;
    flex-wrap: wrap;
  }

  .companions__container {
    width: 40%;
    padding-bottom: 150px;
  }

  .companions__title {
    width: 44%;
    margin: 0;
    margin-bottom: 56px;
  }

  .companions__paragraph {
    width: 95%;
    margin: 0;
    margin-bottom: 40px;
  }

  .companions__condition {
    width: 65%;
    margin: 0;

    span {
      display: block;
    }
  }

  .companions__list {
    margin: 0;
    margin-left: auto;
  }

  .companions__to-all {
    width: 22.5%;
    margin: 0;
    margin-top: -123px;
    padding-top: 30px;
    padding-left: 2.8%;
    align-self: flex-start;

    .companions__to-all-icon {
      margin-left: 17%;
    }
  }
}
