.show-filter-toggle {
  position: relative;
  box-sizing: border-box;
  display: inline-block;
  padding: 0;
  align-self: center;
  word-break: break-all;

  background-color: inherit;

  cursor: pointer;

  & span {
    display: none;
  }

  svg {
    box-sizing: border-box;
    fill: @main-dark-color;
  }

  &:hover {
    color: @main-dark-color-opac-60;

    svg {
      fill: @main-dark-color-opac-60;
    }
  }

  &:active {
    color: @main-dark-color-opac-30;

    svg {
      fill: @main-dark-color-opac-30;
    }
  }
}

.show-filter-toggle__show-icon {
  width: 17px;
  height: 3px;
}

.show-filter-toggle__close-icon {
  width: 12px;
  height: 12px;
}

@media (min-width: @tablet-width) {
  .show-filter-toggle {
    width: 27%;

    color: @main-dark-color;

    text-transform: uppercase;
    background-image: none;

    & span {
      width: 100%;

      .bold-font;
      font-size: 20px;
      line-height: 24px;

      text-align: left;
    }
  }

  .show-filter-toggle--show {
    display: inline-block;

    .show-filter-toggle__show-icon {
      width: 27px;
      height: 5px;
      margin-left: 6.5%;
      margin-bottom: 4px;
      display: inline-block;
    }
  }

  .show-filter-toggle__show-icon {
    position: absolute;
    top: 8px;
    right: -3px;
    width: 27px;
    height: 5px;
  }

  .show-filter-toggle__close-icon {
    position: absolute;
    top: 2px;
    right: 0;
    width: 18px;
    height: 18px;
    margin-left: 32%;
  }
}

@media (min-width: @desktop-width) {
  .show-filter-toggle {
    width: 17.2%;
  }

  .show-filter-toggle__close-icon {
    right: 57px;
  }

  .show-filter-toggle__show-icon {
    right: 50px;
  }
}
