.scroll-menu__button {
  .indents-reset;
  position: relative;
  width: 10%;
  height: 11px;
  margin-top: 4px;

  cursor: pointer;

  border: none;
  background-color: transparent;

  &::before {
    .page-header__button-pseudo;
    background-color: @nav-bth-color;
    top: 0;
  }

  &::after {
    .page-header__button-pseudo;
    background-color: @nav-bth-color;
    bottom: 0;
  }
}
