.select-country-list {
  .list-res;
}

.select-country-list__item {
  position: relative;
  display: flex;

  &:not(:last-of-type) {
    margin-bottom: 22px;
  }

  &::after {
    content: "";
    position: absolute;
    right: 2%;
    width: 23%;
    height: 96%;

    border: 1px solid @border-color;
    border-radius: 5px;
    background-size: 35px 24px;
    background-repeat: no-repeat;
    background-position: 40%;
  }

  &--bosni-gercog::after {
    background-image: url("../img/flag-bosnia.svg");
  }

  &--czech::after {
    background-image: url("../img/flag-czech.svg");
  }

  &--not-selected {
    position: relative;
    width: 98%;
    display: block;

    &::after {
      display: none;
    }

    &::before {
      content: "";
      position: absolute;
      right: 50px;
      top: 18px;
      z-index: 1;
      display: block;
      width: 6px;
      height: 6px;

      background-color: @clear-bg;
      border: 1px solid @pseudo-border-color;
      border-radius: 50%;
    }
  }

  &--selected {
    &::before {
      content: "";
      position: absolute;
      right: 24%;
      top: 18px;
      z-index: 2;
      display: block;
      width: 8px;
      height: 8px;

      border-radius: 50%;
      background-color: @primary-dark-font-color;
    }
  }

  &--add-country {
    width: 98%;

    &::after {
      content: none;
    }

    &::before {
      content: "";
      position: absolute;
      right: 50px;
      top: 18px;
      z-index: 1;
      display: block;
      width: 6px;
      height: 6px;

      border-radius: 50%;
      background-color: @light-bg;
      border: 1px solid @pseudo-border-color;
    }
  }
}

.select-country-list__country {
  box-sizing: border-box;
  position: relative;
  display: block;
  width: 90%;
  padding-left: 6%;
  padding-top: 12px;
  padding-bottom: 9px;

  appearance: none;

  .medium-font;
  font-size: 14px;
  line-height: 18px;
  letter-spacing: 1.3px;
  color: @primary-dark-font-color;

  text-transform: uppercase;

  border: 1px solid @border-color;
  border-radius: 5px;
  background-color: @clear-bg;

  cursor: pointer;

  &::before {
    content: "";
    position: absolute;
    top: 0;
    right: 0;
    display: block;
    width: 30px;
    height: 100%;

    background-color: @clear-bg;
  }

  &--not-selected {
    width: 100%;
    height: inherit;

    padding-left: 5%;
    padding-top: 13px;

    &::before {
      content: none;
    }

    .select-country-list__select-btn-icon {
      right: 6.8%;
    }

    &:hover {
      border-color: @level-range-border-hover;

      .select-country-list__select-btn-icon {
        fill: @level-range-border-hover;
      }
    }
  }
}

.select-country-list__select-wrapper {
  position: relative;
  width: 83%;

  &::after {
    content: "";
    position: absolute;
    right: 11%;
    top: 2px;
    z-index: 1;

    display: block;
    width: 30px;
    height: 90%;

    background-color: @clear-bg;
    box-shadow: -7px 0 2px 0 @box-shadow-btn;
  }
}

.select-country-list__select-btn-icon {
  position: absolute;
  right: 17%;
  top: 19px;
  z-index: 2;
  fill: @font-color-value;
}

.select-country-list__reset-country {
  position: absolute;
  top: -9px;
  right: -5px;
  width: 21px;
  height: 21px;

  border: none;
  background-color: @light-bg;
  border-radius: 50%;

  cursor: pointer;

  .select-country-list__reset-country-icon {
    fill: @font-color-value-20proc-down;
  }

  &--selected {
    .select-country-list__reset-country-icon {
      position: relative;
      top: -1px;
      left: 0;
    }
  }

  &--add-country {
    right: -8px;
  }

  &:hover {
    .select-country-list__reset-country-icon {
      fill: @font-color-value;
    }
  }

  &:active {
    .select-country-list__reset-country-icon {
      fill: @font-color-value-25proc-down;
    }
  }

  &::after {
    content: "";
    position: absolute;
    right: 282%;
    top: 58px;

    display: block;
    width: 5px;
    height: 10px;

    background-color: transparent;
    background-image: url("../img/small-arrow.svg");
  }
}

.select-country-list__select-letter {
  position: absolute;
  top: 42px;
  right: -1px;
  z-index: 2;
}

.step-list__submit-button {
  .bold-font;
  font-size: 17px;
  line-height: 30px;
  color: @secondary-color-font-button;

  text-transform: uppercase;

  background-color: @yellow-button-color;
  border: none;
}

.select-country-list__close-alphabet {
  position: absolute;
  top: 17px;
  right: 8%;
  z-index: 1;

  fill: @clear-bg;

  opacity: 0.3;

  cursor: pointer;

  &:hover {
    opacity: 1;
  }

  &:active {
    opacity: 0.1;
  }
}

.select-country-list__check {
  & ~ .select-country-list__select-letter {
    display: none;
  }

  &:checked ~ .select-country-list__select-letter {
    display: block;
  }

  &:checked ~ .select-country-list__country--not-selected {
    position: relative;
    z-index: 1;
    padding-bottom: 11px;

    color: @clear-bg;

    background-color: @primary-dark-font-color;
    border: none;
    border-radius: 0;
    border-top-left-radius: 5px;
    border-top-right-radius: 5px;

    &::before {
      content: none;
    }

    .select-country-list__select-btn-icon {
      display: none;
    }
  }

  &:checked ~ .select-country-list__reset-country {
    display: none;
  }

  & .select-country-list__select-btn-icon {
    display: block;
  }

  & .select-country-list__close-alphabet {
    display: none;
  }
}

.select-country-list__hidden-letter-table:checked ~ .select-country-list__select-letter {
  display: none;
}

@media (min-width: @tablet-width) {
  .select-country-list {
    width: 70%;
    padding-top: 41px;
    padding-right: 4.5%;

    border-right: 1px solid @form-border-second-value;
  }

  .select-country-list__select-wrapper {
    width: 99%;

    .select-country-list__select-btn-icon {
      right: 7.1%;
      top: 30px;
      width: 17px;
      height: 9px;
    }

    &::after {
      content: none;
    }
  }

  .select-country-list__item {
    position: relative;
    display: block;

    &--selected {
      &::before {
        right: -8%;
        top: 26px;
        width: 15px;
        height: 15px;
      }
    }

    &--not-selected {
      width: 99%;

      &::before {
        right: -9.5%;
        top: 27px;
        width: 12px;
        height: 12px;
      }
    }

    &--add-country {
      &::before {
        right: -10.4%;
        top: 26px;
        width: 12px;
        height: 12px;
      }
    }

    &:first-of-type {
      margin-bottom: 15px;
    }

    &:nth-of-type(2) {
      margin-bottom: 15px;
    }

    &:nth-of-type(3) {
      margin-bottom: 14px;
    }

    &::after {
      position: absolute;
      top: 13px;
      right: -33%;
      width: 19%;
      height: 65%;

      border: none;
      background-size: contain;
      background-position: -1px;
    }
  }

  .select-country-list__reset-country {
    top: 26px;
    right: -43%;
    padding: 0;

    background-color: transparent;

    .select-country-list__reset-country-icon {
      left: 3px;
      width: 18px;
      height: 18px;
    }

    &--add-country {
      right: -45.2%;
    }

    &::after {
      content: none;
    }
  }

  .select-country-list__country {
    position: relative;
    width: 100%;
    padding-top: 27px;
    padding-bottom: 23px;
    padding-left: 5.9%;

    font-size: 20px;
    line-height: 18px;
    letter-spacing: 1.7px;

    &--not-selected {
      width: 100%;
      padding-top: 26px;
      padding-left: 6%;
      padding-bottom: 25px;

      .select-country-list__select-btn-icon {
        top: 29px;
        width: 17px;
        height: 9px;
      }
    }
  }

  .select-country-list__check:checked ~ .select-country-list__select-letter {
    display: flex;
  }

  .select-country-list__check:checked ~ .select-country-list__country--not-selected {
    padding-top: 27px;
    padding-bottom: 26px;
    padding-left: 7%;
  }

  .select-country-list__close-alphabet {
    top: 25px;
    right: 7%;
    width: 18px;
    height: 18px;
  }

  .select-country-list__select-letter {
    left: -0.2%;
    top: 69px;
  }
}

@media (min-width: @desktop-width) {
  .select-country-list {
    width: 72.8%;
    padding-top: 62px;
    padding-right: 47px;
  }

  .select-country-list__item {
    max-width: 731px;

    &:first-of-type {
      margin-bottom: 15px;
    }

    &::after {
      right: -159px;
      width: 70px;
    }

    &--selected {
      &::before {
        right: -55px;
      }
    }

    &--not-selected {
      &::before {
        right: -62px;
      }
    }

    &--add-country {
      &::before {
        right: -70px;
      }
    }
  }

  .select-country-list__select-wrapper {
    width: 731px;

    .select-country-list__select-btn-icon {
      right: 26px;
      top: 31px;
    }
  }

  .select-country-list__country {
    padding-left: 23px;

    &--not-selected {
      width: 731px;
      padding-left: 25px;
      padding-bottom: 23px;
    }
  }

  .select-country-list__select-letter {
    left: 0;
  }

  .select-country-list__country--not-selected .select-country-list__select-btn-icon {
    right: 26px;
  }

  .select-country-list__reset-country {
    right: -266px;

    &--add-country {
      right: -276px;
    }
  }

  .select-country-list__check:checked ~ .select-country-list__country--not-selected {
    padding-left: 25px;
  }

  .select-country-list__close-alphabet {
    right: 2%;
  }
}
