.page-footer {
  box-sizing: border-box;
  z-index: 1;
  width: 100%;
  margin-top: -244px;
  position: relative;

  background-color: @clear-bg;

  border-top-left-radius: 30px;
  border-top-right-radius: 30px;

  &--catalog {
    margin: 0;
  }

  &--form {
    margin: 0;
    margin-top: -20px;
  }
}

.page-footer__wrapper {
  box-sizing: border-box;
  width: 84%;
  margin-left: auto;
  margin-right: auto;
  padding-top: 37px;
  padding-bottom: 20px;

  &--catalog {
    padding-top: 39px;
  }
}

.page-footer__logo {
  display: block;
  width: 96px;
  height: 16px;
  margin-bottom: 40px;
  margin-left: auto;
  margin-right: auto;
}

.page-footer__logo-image {
  width: 96px;
  height: 18px;
}

.page-footer__social-list {
  margin-bottom: 37px;
}

@media (min-width: @tablet-width) {
  .page-footer {
    margin-top: -150px;

    border-top-left-radius: 60px;
    border-top-right-radius: 63px;

    &--catalog {
      margin: 0;
    }

    &--form {
      margin-top: -21px;
    }
  }

  .page-footer__wrapper {
    width: 89.4%;
    padding-top: 49px;
    padding-bottom: 46px;
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
  }

  .page-footer__logo {
    display: block;
    width: 164px;
    height: 43px;
    margin: 0;
    padding-top: 8px;
  }

  .page-footer__logo-image {
    width: 164px;
    height: 42px;
  }

  .page-footer__social-list {
    margin-top: 0;
    margin-bottom: 0;
    margin-left: 13%;
  }
}

@media (min-width: @desktop-width) {
  .page-footer {
    margin-top: 0;

    &--index {
      margin-top: -116px;
    }

    &--form {
      margin-top: -93px;
    }
  }

  .page-footer__wrapper {
    width: 93%;
    max-width: 1462px;
    padding-top: 47px;

    &--catalog {
      padding-top: 48px;
    }
  }

  .page-footer__logo {
    display: block;
    width: 198px;
    height: 52px;
    margin: 0;
    padding-top: 3px;
    padding-left: 10px;

    &--form {
      padding-top: 4px;
      padding-left: 12px;
    }
  }

  .page-footer__logo-image {
    width: 198px;
    height: 52px;
  }

  .page-footer__social-list {
    margin-left: 27.5%;
  }
}
