.calendar {
  box-sizing: border-box;
  width: 100%;
  padding-top: 22px;
}

.calendar__month-value {
  margin-bottom: 21px;
  padding-top: 12px;
  padding-bottom: 9px;
  display: flex;


  border-top: 1px solid @border-color;
  border-bottom: 1px solid @border-color;
}

.calendar__value {
  box-sizing: border-box;
  .indents-reset;
  flex-grow: 1;

  .bold-font;
  font-size: 20px;
  line-height: 20px;
  color: @primary-dark-font-color;

  word-break: break-all;

  text-align: center;
}

.calendar__month-button {
  margin: 0;
  padding: 0;

  background-color: @clear-bg;
  border: none;

  cursor: pointer;

  &--increase {
    .calendar__pagination-icon {
      transform: rotate(180deg);
    }
  }

  .calendar__pagination-icon {
    fill: @font-color-value;
  }

  &:hover {
    .calendar__pagination-icon {
      fill: @font-color-value-20proc-up;
    }
  }

  &:active {
    .calendar__pagination-icon {
      fill: @font-color-value-20proc-down;
    }
  }
}

.calendar__weekdays {
  .list-res;
  padding-bottom: 8px;
  display: flex;
}

.calendar__weekday-item {
  width: calc(100% / 7);

  .regular-font;
  font-size: 14px;
  line-height: 18px;

  text-align: center;
  text-transform: uppercase;

  span {
    display: none;
  }

  &:nth-last-of-type(2),
  &:last-of-type {
    color: @weekend-color-font;
  }
}

.calendar__days {
  .list-res;
  display: flex;
  flex-wrap: wrap;

  border-top: 1px solid @border-color;
  border-left: 1px solid @border-color;
}

.calendar__date {
  box-sizing: border-box;
  position: relative;
  display: block;
  width: calc(100% / 7);

  .regular-font;
  font-size: 14px;
  line-height: 18px;

  text-align: center;

  border-right: 1px solid @border-color;
  border-bottom: 1px solid @border-color;

  input[type="checkbox"] ~ label {
    &:hover {
      background-color: @active-date;
      color: @clear-font;
    }

    display: block;

    background-color: @available-days;
    color: @basic-font;
  }

  input[type="checkbox"]:disabled ~ label {
    color: @no-active-days;
    background-color: @clear-bg;
  }

  label {
    position: relative;

    padding-top: 8px;
    padding-bottom: 4px;

    cursor: pointer;
  }

  input[type="checkbox"]:checked ~ label {
    display: block;

    background-color: @hover-tooltype-bg;
    color: @clear-font;
  }

  label:hover {
    &::after {
      content: "";
      box-sizing: border-box;
      position: absolute;
      z-index: 0;
      top: -1px;
      left: -1px;
      display: block;
      width: calc(100% + 2px);
      height: calc(100% + 2px);

      border: 2px solid @select-date;
    }
  }
}

.calendar__plane {
  visibility: hidden;
  position: absolute;
}

.calendar__month {
  position: absolute;
  visibility: hidden;
}

@media (min-width: @tablet-width) {
  .calendar__value {
    font-size: 36px;
  }

  .calendar__month-button {
    position: relative;
    width: 20%;

    svg {
      position: absolute;
      width: 19px;
      height: 34px;
    }

    &--decrease {
      svg {
        top: -10px;
        left: 12%;
      }
    }

    &--increase {
      svg {
        top: -10px;
        left: 70%;
      }
    }
  }

  .calendar__month-value {
    margin-bottom: 34px;
    padding-top: 36px;
    padding-bottom: 29px;
    font-size: 36px;
  }

  .calendar__weekdays {
    padding-bottom: 2px;
  }

  .calendar__weekday-item {
    font-size: 18px;
    line-height: 30px;

    span {
      display: inline-block;
      text-transform: none;
    }
  }

  .calendar__date {
    .medium-font;
    font-size: 20px;
    line-height: 18px;

    label {
      padding-top: 25px;
      padding-bottom: 25px;
    }

    label:hover {
      &::after {
        border-width: 4px;
      }
    }

    &--select {
      &::after {
        width: calc(100% + 2px);
        height: calc(100% + 2px);

        border: 4px solid @select-date;
      }
    }
  }
}

@media (min-width: @desktop-width) {
  .calendar {
    padding-top: 33px;
  }

  .calendar__value {
    padding-top: 30px;
  }

  .calendar__month-value {
    margin-bottom: 0;
    padding: 0;
    padding-top: 6px;
    padding-bottom: 29px;
  }

  .calendar__month-button {
    &--decrease {
      svg {
        top: 22px;
        left: 3px;
      }
    }

    &--increase {
      svg {
        top: 22px;
        left: 177px;
      }
    }
  }

  .calendar__dates-table {
    padding-left: 3px;
    padding-right: 3px;
  }

  .calendar__weekdays {
    padding-top: 11px;
    padding-bottom: 14px;
  }

  .calendar__weekday-item {
    padding-left: 1.7%;

    text-align: left;
  }

  .calendar__date {
    font-size: 18px;
    line-height: 30px;

    text-align: left;

    label {
      padding: 0;
      padding-top: 6px;
      padding-bottom: 34px;
      padding-left: 16px;
    }
  }

  .calendar__plane {
    bottom: 8px;
  }

  .calendar__check-into:checked ~ label {
    .calendar__plane {
      visibility: visible;
    }
  }

  .calendar__check-out:checked ~ label {
    .calendar__plane {
      visibility: visible;
    }
  }

  .calendar__month {
    visibility: initial;
    display: inline-block;

    .regular-font;
    font-size: 18px;
    line-height: 30px;
  }
}
