.level {
  box-sizing: border-box;
  width: 60px;
  padding-bottom: 5px;

  border-radius: 50%;
  background-color: @level-bg-60opac;

  &--travelmates {
    position: relative;
    width: 30%;
    padding-bottom: 0;

    background-color: @clear-bg;

    .level__level-ring {
      position: relative;
      top: -3px;
      left: 26%;
      transform: rotate(270deg);
    }
  }

  &--profile {
    position: relative;
    width: 100px;
    height: 100px;

    background-color: transparent;

    .level__level-ring {
      position: absolute;
      top: -1px;

      stroke-width: 2px;
    }
  }
}

.level__level-ring-wrapper {
  position: relative;
  width: 70px;
  margin-right: 10%;
  margin-left: auto;
}

.level__title {
  position: relative;
  top: -9px;
  left: 13%;
  width: 80%;
  display: block;
  margin: 0;

  .regular-font;
  font-size: 15px;
  line-height: 18px;

  word-break: break-all;

  text-align: center;
  background-color: @clear-bg;
}

.level__level-ring {
  position: absolute;
  stroke: @level-color;
  stroke-width: 3px;

  transform: rotate(270deg);
}

.level__value {
  position: relative;
  .indents-reset;
  width: 26px;
  margin-top: 13px;
  margin-left: 15px;
  margin-right: auto;

  .bold-font;
  font-size: 24px;
  line-height: 24px;
  color: @clear-font;

  &::after {
    position: absolute;
    top: 22px;
    right: -4px;
    width: 31px;
    height: 1px;
    content: "";

    background-color: rgba(255, 255, 255, 0.3);
  }

  &--demin {
    margin-top: 14px;
    margin-left: 17px;

    &::after {
      top: 23px;
      right: -3px;
    }
  }

  &--travelmates {
    position: absolute;
    top: 10px;
    right: 5%;
    width: 40px;
    margin: 0;
    color: @primary-dark-font-color;

    word-break: break-all;

    &::after {
      content: none;
    }
  }

  &--profile {
    width: 41%;
    margin: 0;
    margin-left: 28%;
    margin-top: 22px;

    font-size: 38px;
    line-height: 38px;
    color: @secondary-color-font-button;

    border: none;
    background-color: transparent;
  }

  &--profile::after {
    width: 49px;
    height: 2px;
    top: 35px;
    background-color: @border-color;
  }
}

.level__unit {
  .indents-reset;
  width: 23px;
  margin-left: 16px;
  margin-right: auto;
  padding-top: 1px;

  .regular-font;
  font-size: 14px;
  line-height: 14px;
  color: @clear-font;

  &--demin {
    margin-left: 17px;
    padding-top: 2px;
  }

  &--profile {
    .indents-reset;
    position: relative;
    display: block;
    width: 34%;
    margin-left: auto;
    margin-right: auto;

    font-size: 18px;
    line-height: 22px;
    color: inherit;

    &::before {
      content: "";
      left: -8px;
      top: -4px;
      position: absolute;
      width: 49px;
      height: 2px;
      display: block;

      background-color: @border-color;
    }
  }

  &--travelmates {
    display: none;
  }
}

@media (min-width: @tablet-width) {
  .level {
    &--profile {
      width: 94px;
      height: 94px;

      .level__level-ring {
        top: 1px;
        left: 2%;
      }

      .level__value {
        margin-left: 28%;
      }

      .level__unit {
        margin-left: 35%;
      }
    }

    &--travelmates {
      width: 60px;
      height: 60px;
      margin: 0;

      .level__level-ring {
        top: -2px;
        left: 0;
      }
    }
  }

  .level__title {
    display: none;
  }

  .level__level-ring {
    width: 60px;
    height: 60px;

    &--profile {
      width: 94px;
      height: 94px;
    }
  }

  .level__value {
    margin-left: 17px;

    &::after {
      right: 11px;
      top: 23px;
    }

    &--profile {
      width: 46%;
    }

    &--travelmates {
      position: relative;
      top: -55px;
      right: 0;

      font-size: 24px;
      line-height: 24px;

      &::after {
        content: "";
        top: 22px;

        background-color: @font-color-value-20proc-down;
      }
    }
  }

  .level__unit {
    margin-left: 17px;
    padding-top: 2px;

    &--travelmates {
      position: relative;
      left: 0;
      top: -55px;
      margin-left: 28%;
      display: block;

      color: @font-color-value-10proc-up;
    }
  }
}

@media (min-width: @desktop-width) {
  .level {
    padding-bottom: 0;

    &--travelmates {
      .level__level-ring {
        top: -1px;
      }
    }

    &--user-cart {
      height: 59px;
    }
  }

  .level__value {
    &--profile {
      color: @clear-bg;

      &::after {
        border-color: @clear-bg;
      }
    }

    &--travelmates {
      right: 2px;

      &::after {
        top: 24px;
      }
    }
  }

  .level__unit {
    &--profile {
      padding-top: 0;
      color: @clear-bg;

      &::before {
        top: -5px;
      }
    }
  }

  .level__level-ring {
    &--user-cart {
      top: -1px;
    }
  }
}
