.travelmates {
  padding: 0;

  list-style: none;
}

.travelmates__item {
  box-sizing: border-box;
  position: relative;
  width: 85%;
  margin-left: auto;
  margin-right: auto;
  margin-bottom: 10px;
  padding-top: 14px;
  padding-bottom: 22px;
  display: flex;
  flex-wrap: wrap;

  background-color: @clear-bg;
  border-radius: 20px;

  &--smolov {
    padding-top: 14px;
  }
}

.travelmates__profile-link {
  margin-left: 7%;
}

.travelmates__title {
  position: relative;
  .indents-reset;
  width: 38%;
  margin-left: 6%;

  &--offline {
    &::before {
      content: "";
      position: absolute;
      right: -40.5%;
      top: 9px;

      width: 6px;
      height: 6px;

      background-color: @offline-user-color;
      border-radius: 50%;
    }
  }

  &--online {
    &::after {
      content: "";
      position: absolute;
      right: -39%;
      top: 10px;

      width: 6px;
      height: 6px;

      background-color: @online-user-color;

      border-radius: 50%;
    }
  }

  &:hover {
    opacity: 0.6;
  }

  &:active {
    opacity: 0.3;
  }
}

.travelmates__link {
  .bold-font;
  font-size: 20px;
  line-height: 20px;
  color: @primary-dark-font-color;

  word-wrap: break-word;

  &:hover {
    color: @primary-dark-font-hover;
  }

  &:active {
    color: @primary-dark-font-active;
  }
}

.travelmates__likes {
  position: absolute;
  left: 25%;
  top: 53px;
  z-index: 1;
}

.travelmates__avatar {
  width: 70px;
  height: 70px;
  border-radius: 50%;
}

.travelmates__hashtags {
  margin-top: 9px;
  margin-left: 9.3%;
  margin-right: 9%;
  margin-bottom: 4px;
}

.travelmates__wish {
  margin-bottom: 19px;

  &--demin {
    margin-bottom: 25px;
  }

  &--smolov {
    margin-bottom: 22px;
  }

  &--rogovaya {
    margin-bottom: 24px;
  }
}

.travelmates__transportation {
  align-self: flex-start;
}

.travelmates__wrapper-for-level {
  box-sizing: border-box;
  width: 100%;
  margin-bottom: 3px;
  display: flex;

  border-top: 1px solid @light-bg;
}

.travelmates__come-on-button {
  box-sizing: border-box;
  display: block;
  width: 85%;
  margin-left: auto;
  margin-right: auto;
  padding-top: 8px;
  padding-bottom: 3px;
  padding-left: 29%;
  padding-right: 1%;

  .bold-font;
  font-size: 17px;
  line-height: 30px;
  color: @secondary-color-font-button;

  text-transform: uppercase;

  word-break: break-all;

  background-color: @yellow-button-color;
  border-radius: 20px;

  .button-actions;

  &--rogovaya {
    padding-bottom: 4px;
  }
}

@media (min-width: @tablet-width) {
  .travelmates {
    margin-bottom: 45px;
  }

  .travelmates__item {
    position: relative;
    width: 89%;
    min-height: auto;
    padding: 0;
    padding-top: 32px;
    display: flex;
    flex-direction: row;

    &:not(:last-of-type) {
      margin-bottom: 40px;
    }

    &--smolov {
      padding-top: 32px;
    }
  }

  .travelmates__title {
    margin-bottom: 18px;
    margin-left: 37.2%;
    margin-right: auto;
    order: -4;

    &::before {
      width: 7px;
      height: 7px;
      right: 15.9%;
      top: 10px;
    }

    &::after {
      width: 7px;
      height: 7px;
      right: 6%;
    }

    &--demin {
      &::before {
        left: 70%;
      }
    }

    &--smolov {
      &::before {
        left: 78%;
      }
    }
  }

  .travelmates__link {
    font-size: 30px;
    line-height: 30px;
  }

  .travelmates__profile-link {
    position: absolute;
    left: 5%;
    top: 35px;
    margin: 0;
  }

  .travelmates__avatar {
    width: 190px;
    height: 190px;

    border-radius: 18px;
  }

  .travelmates__hashtags {
    margin: 0;
    margin-left: 38%;
    margin-right: auto;
    margin-bottom: 44px;
    order: -3;
  }

  .travelmates__come-on-button {
    width: 21.6%;
    margin: 0;
    margin-left: 37%;
    padding-top: 13px;
    padding-bottom: 8px;
    padding-left: 4.3%;
    order: -2;

    font-size: 20px;
    line-height: 30px;

    border-radius: 25px;

    &--smolov {
      padding-bottom: 7px;
      padding-top: 12px;
    }
  }

  .travelmates__likes {
    position: relative;
    top: 0;
    left: 0;
    width: 16%;
    margin: 0;
    order: -1;
  }

  .travelmates__wrapper-for-level {
    position: absolute;
    top: 37px;
    right: 22px;
    width: 9%;
    display: flex;
    flex-direction: column;

    border-top: none;

    &--smolov {
      top: 35px;
      right: 23px;
    }
  }

  .travelmates__wish {
    margin: 0;
    margin-top: 24px;
    padding-bottom: 20px;
  }
}

@media (min-width: @desktop-width) {
  .travelmates {
    width: 72%;
    margin: 0;
    padding-top: 0;
  }

  .travelmates__item {
    width: 100%;
    margin: 0;
    padding-top: 46px;
    padding-bottom: 47px;
    flex-direction: row;

    &:not(:last-of-type) {
      margin-bottom: 30px;
    }

    &:nth-of-type(3) {
      margin-bottom: 30px;
    }

    &--rogovaya {
      padding-top: 46px;
    }
  }

  .travelmates__title {
    width: 30%;
    margin: 0;
    margin-left: 342px;
    margin-right: 34%;
    margin-bottom: 20px;

    &::before {
      left: -24px;
      top: 12px;
    }

    &::after {
      right: 106%;
      top: 13px;
    }

    &--smolov {
      margin-bottom: 19px;
    }
  }

  .travelmates__avatar {
    width: 285px;
    height: 285px;

    border-radius: 0;
    border-top-left-radius: 20px;
    border-bottom-left-radius: 20px;
  }

  .travelmates__likes {
    margin: 0;
  }

  .travelmates__hashtags {
    width: 26%;
    margin: 0;
    margin-left: 346px;
    margin-right: 34%;
    margin-bottom: 44px;
    order: -3;
  }

  .travelmates__profile-link {
    position: absolute;
    top: 0;
    left: 0;
    width: 30%;
    height: 100%;

    &::after {
      content: "";
      position: absolute;
      left: 221%;
      top: 47px;
      display: block;
      width: 1px;
      height: 187px;

      background: linear-gradient(to top, transparent 5%, @dec-bg, transparent 95%);
    }
  }

  .travelmates__come-on-button {
    width: 15.5%;
    margin-left: 340px;
    padding-left: 30px;
    display: block;
    order: -2;
  }

  .travelmates__wrapper-for-level {
    position: absolute;
    width: 30%;
    top: 183px;
    right: 16px;
    flex-direction: row;
  }

  .travelmates__wish {
    position: absolute;

    &--smolov {
      margin-top: 22px;
    }
  }
}
