.letter-table {
  .list-res;
  box-sizing: border-box;
  width: 82.4%;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;

  border-left: 1px solid @border-color;
  border-top: 1px solid @border-color;
  background-color: @clear-bg;

  &--form {
    width: 99%;
  }
}

.letter-table__item {
  box-sizing: border-box;
  position: relative;
  width: calc(100% / 5);

  border-right: 1px solid @border-color;
  border-bottom: 1px solid @border-color;

  &--active::before {
    content: "";
    position: absolute;
    top: -1px;
    left: -2px;
    z-index: 1;
    width: calc(100% + 2px);
    height: calc(100% + 2px);

    background-color: @primary-dark-font-color;
  }

  &--active-form {
    &::before {
      content: "";
      position: absolute;
      top: -1px;
      left: -2px;
      z-index: 1;
      width: calc(100% + 2px);
      height: calc(100% + 2px);

      background-color: @light-bg;
    }
  }

  &--form {
    position: relative;

    &:hover {
      &::after {
        content: "";
        box-sizing: border-box;
        position: absolute;
        top: -1px;
        left: -1px;
        display: block;
        width: calc(100% + 2px);
        height: calc(100% + 2px);

        border: 1px solid @level-range-border-hover;
      }
    }

    &:active {
      &::after {
        content: "";
        box-sizing: border-box;
        position: absolute;
        top: -1px;
        left: -1px;
        display: block;
        width: calc(100% + 2px);
        height: calc(100% + 2px);

        border: none;
        background-color: @primary-dark-font-color;
      }

      .letter-table__letter--form {
        position: relative;
        z-index: 1;
        color: @clear-bg;
      }
    }
  }
}

.letter-table__letter {
  box-sizing: border-box;
  display: block;
  padding-top: 12px;
  padding-bottom: 10px;

  .medium-font;
  font-size: 14px;
  line-height: 18px;
  letter-spacing: 1.4px;
  color: @primary-dark-font-color;

  text-transform: uppercase;
  text-align: center;

  background-color: transparent;

  &--active {
    position: relative;
    z-index: 1;
    color: @clear-font;
  }

  &--active-form {
    position: relative;
    z-index: 1;
    color: @primary-dark-font-color;
  }
}

.letter-table__letter--active.letter-table__letter--form {
  color: @primary-dark-font-color;
}

.letter-table__countries-list {
  margin: 0;
  display: none;
}

@media (min-width: @tablet-width) {
  .letter-table {
    width: 62.8%;

    background-color: transparent;
    border: none;

    &--form {
      width: 66.1%;
      padding-top: 36px;
      padding-bottom: 36px;
      padding-left: 4.7%;
      padding-right: 12.6%;
      align-self: flex-start;

      background-color: @clear-bg;
    }
  }

  .letter-table__item {
    width: 78px;
    margin-bottom: 33px;
    padding-left: 2.5%;
    padding-top: 26px;

    border: none;

    &--active::before {
      background-color: transparent;
    }

    &--active-form::before {
      z-index: 0;

      background-color: @primary-dark-font-color;
    }

    &:nth-last-of-type(2) {
      padding-left: 0.5px;
    }

    &--form {
      width: calc(100% / 5);
      margin-bottom: 0;
      margin-top: -1px;
      margin-left: -1px;
      padding: 0;

      border: 1px solid @border-color;

      &:hover {
        &::after {
          top: 0;
          left: 0;
          display: block;
          width: calc(100% + 2px);
          height: calc(100% + 2px);

          border: 1px solid @level-range-border-hover;
        }
      }

      &:active {
        &::after {
          top: 0;
          left: 0;
        }
      }
    }
  }

  .letter-table__letter {
    padding: 0;

    .bold-font;
    font-size: 60px;
    line-height: 24px;
    color: @main-dark-color-opac-20;

    text-align: left;

    &--active {
      color: @main-dark-color;
      background-color: transparent;
    }

    &--form {
      padding-top: 27px;
      padding-bottom: 23px;

      font-size: 20px;
      line-height: 18px;
      color: @primary-dark-font-color;

      text-align: center;
    }

    &--active-form {
      position: relative;
      z-index: 1;

      color: @clear-font;
    }
  }
}

@media (min-width: @desktop-width) {
  .letter-table {
    width: 98%;
    max-width: 1462px;

    &--form {
      width: 550px;
      padding: 0;
      padding-top: 34px;
      padding-bottom: 17px;
      padding-left: 27px;
      padding-right: 102px;
      align-self: flex-start;

      border-bottom-left-radius: 20px;
    }
  }

  .letter-table__item {
    width: 251px;
    margin: 0;
    margin-bottom: 31px;
    padding-left: 0.6%;
    padding-top: 29px;

    &:first-of-type {
      padding-left: 1.3%;
      padding-top: 29px;
    }

    &:nth-of-type(6) {
      margin-bottom: 20px;
    }

    &:nth-of-type(11) {
      margin-bottom: 67px;
    }

    &--form {
      width: calc((100% - 60px) / 5);
      margin-bottom: 15px;
      padding: 0;

      border-radius: 5px;

      &:nth-of-type(11) {
        margin-bottom: 15px;
      }

      &:nth-of-type(6) {
        margin-bottom: 15px;
      }

      &:first-of-type {
        padding: 0;
      }

      &:not(:nth-of-type(5n)) {
        margin-right: 15px;
      }

      &:hover {
        &::after {
          border-radius: 5px;
        }
      }
    }

    &--active&--form::before {
      border-radius: 5px;
    }
  }

  .letter-table__letter {
    padding: 0;
    padding-top: 8px;
    padding-left: 7px;

    color: @main-dark-color;

    &--active {
      padding-left: 0;
    }

    &--ae {
      padding-left: 4%;
    }

    &--u {
      padding-left: 7%;
    }

    &--o {
      padding-top: 2px;
    }

    &--form {
      padding: 0;
      padding-top: 26px;
      padding-bottom: 24px;
    }

    &--active-form {
      position: relative;
      z-index: 1;

      color: @clear-font;
    }
  }

  .letter-table__countries-list {
    display: block;
    margin-top: 24px;
    padding-left: 2%;
  }
}
