.review {
  position: relative;
  z-index: 2;
  width: 58%;
  background-color: transparent;
}

.review__item {
  .indents-reset;
}

.review__text {
  .indents-reset;
  margin-bottom: 19px;

  color: @clear-font;
}

.review__author {
  margin-bottom: 6px;

  .bold-font;
  font-style: normal;
  color: @clear-font;
}

.review__author-position {
  .indents-reset;

  .bold-font;
  color: @clear-font;
}

@media (min-width: @tablet-width) {
  .review {
    width: 100%;
  }

  .review__text {
    width: 45%;
    margin-bottom: 44px;

    font-size: 28px;
    line-height: 33px;
  }

  .review__author {
    margin-bottom: 5px;

    font-size: 21px;
    line-height: 25px;
  }

  .review__author-position {
    width: 38%;
    font-size: 21px;
    line-height: 25px;
  }
}

@media (min-width: @desktop-width) {
  .review {
    width: 20%;
  }

  .review__item {
    padding-left: 2px;
  }

  .review__text {
    width: 100%;
    margin-bottom: 45px;
  }

  .review__author-position {
    width: 100%;
  }
}
