.countries-list {
  padding: 0;

  .regular-font;

  list-style: none;

  &--one-column {
    margin: 0;

    .countries-list__item:not(:last-of-type) {
      margin-bottom: 0;
    }
  }

  &--one-column-form {
    background-color: @clear-bg;
  }
}

.countries-list__item {
  &:not(:last-of-type) {
    margin-bottom: 6px;
  }

  a {
    color: inherit;

    word-break: break-all;
  }

  &--form {
    a {
      .regular-font;
    }

    a:hover {
      color: @primary-dark-font-color;
    }

    a:active {
      color: @font-color-value;
    }
  }

  &--alg {
    display: none;
  }
}

@media (min-width: @tablet-width) {
  .countries-list {
    margin: 0;

    &--one-column {
      width: 155px;

      .countries-list__item {
        &:not(:last-of-type) {
          margin-bottom: 2px;
        }
      }
    }
  }

  .countries-list__item {
    a {
      font-size: 20px;
      line-height: 28.5px;
    }

    &:not(:last-of-type) {
      margin-bottom: 2px;
    }
  }
}

@media (min-width: @desktop-width) {
  .countries-list {
    height: 100%;
    padding-top: 48px;
    padding-left: 5%;

    &--letter-a {
      padding-left: 0;
    }

    &--letter-d {
      padding-left: 1.5%;
    }

    &--letter-ch {
      padding-top: 52px;
      padding-left: 4%;
    }

    &--letter-sh {
      padding-top: 52px;
    }

    &--letter-ae {
      padding-top: 52px;
    }

    &--letter-o {
      padding-top: 56px;
    }

    &--u {
      padding-top: 52px;
      padding-left: 9%;
    }

    &--ya {
      padding-top: 52px;
    }

    &--one-column {
      display: none;
    }

    &--one-column-form {
      padding-top: 31px;
      padding-left: 25px;
      padding-bottom: 115px;
      display: flex;
      flex-direction: column;

      border-left: 1px solid @countries-list-border;
    }
  }

  .countries-list--one-column .countries-list__item:not(:last-of-type) {
    margin-bottom: 0;
  }

  .countries-list__item {
    font-size: 20px;
    line-height: 30px;

    &:not(:last-of-type) {
      margin-bottom: 0;
    }

    &--afg {
      order: -9;
    }

    &--alb {
      order: -8;
    }

    &--alg {
      display: block;
      order: -7;
    }

    &--andor {
      order: -6;
    }

    &--arg {
      order: -5;
    }

    &--arm {
      order: -4;
    }

    &--algir {
      display: none;
    }

    &--angola {
      display: none;
    }

    &--barbuda {
      display: none;
    }
  }
}
