.profile-info {
  background-color: @form-bg;
}

.profile-info__wrapper {
  box-sizing: border-box;
  width: 84%;
  margin-left: auto;
  margin-right: auto;
  padding-top: 23px;
  display: flex;
  flex-wrap: wrap;
}

.profile-info__level {
  margin-left: 7%;
}

.profile-info__photo-wrapper {
  margin-left: 12%;
}

.profile-info__avatar {
  border-radius: 50%;
}

.profile-info__button {
  margin-top: 11px;
  margin-bottom: 16px;
}

.profile-info__hashtags-area {
  margin-bottom: 18px;
}

@media (min-width: @tablet-width) {
  .profile-info__wrapper {
    width: 89%;
    padding-top: 64px;
    padding-left: 4%;
    padding-right: 4%;
  }

  .profile-info__level {
    margin: 0;
    margin-bottom: 39px;
    order: -2;
  }

  .profile-info__photo-wrapper {
    margin: 0;
  }

  .profile-info__trasportation {
    margin-left: auto;
    margin-right: auto;
    order: -1;
  }

  .profile-info__button {
    margin-top: 13px;
    margin-bottom: 32px;
  }

  .profile-info__hashtags-area {
    margin: 0;
  }
}

@media (min-width: @desktop-width) {
  .profile-info__wrapper {
    position: relative;
    width: 91.5%;
    max-width: 1462px;
    padding: 0;
    padding-left: 4.3%;
  }

  .profile-info__level {
    position: absolute;
    top: -85%;
    right: 25%;
    z-index: 3;
  }

  .profile-info__photo-wrapper {
    z-index: 3;
    margin-left: 77.4%;
    margin-right: 7%;
    margin-top: -190px;
    order: -4;
  }

  .profile-info__button {
    margin: 0;
    margin-left: 77.4%;
    margin-right: 7%;
    margin-bottom: 26px;
    order: -3;
  }

  .profile-info__trasportation {
    margin: 0;
    order: -1;
  }

  .profile-info__hashtags-area {
    margin: 0;
    margin-right: auto;
    order: -2;
  }
}
