.companions-value {
  display: flex;
  flex-wrap: wrap;
}

.companions-value__paragraphs-wrapper {
  width: 100%;
  padding-bottom: 8px;
  display: flex;
  justify-content: space-between;
}

.companions-value__requirement {
  margin: 0;
  margin-right: 15.5%;
  padding-bottom: 9px;

  .input-field-value-title;
  letter-spacing: 1.4px;
  color: @primary-dark-font-color;

  word-break: break-all;
}

.companions-value__value {
  margin: 0;
  .input-field-value-title;
  color: @font-color-value;

  word-break: break-all;
}

@media (min-width: @tablet-width) {
  .companions-value {
    border-top: 1px solid @form-border-second-value;
  }

  .companions-value__paragraphs-wrapper {
    padding-top: 25px;
    padding-bottom: 25px;
  }

  .companions-value__requirement {
    margin: 0;
    margin-right: 56.5%;
    padding-top: 25px;
    padding-bottom: 24px;

    font-size: 20px;
    line-height: 18px;
  }

  .companions-value__value {
    margin: 0;
    padding-top: 25px;
    padding-bottom: 24px;

    font-size: 20px;
    line-height: 18px;
  }
}

@media (min-width: @desktop-width) {
  .companions-value {
    width: 50%;
    padding-top: 30px;
    display: flex;

    border: none;
  }

  .companions-value__input {
    margin-right: 7%;
    order: -1;
    align-self: flex-start;
  }

  .companions-value__requirement {
    margin: 0;
    margin-right: 5.4%;
    align-self: flex-start;
    order: -2;

    letter-spacing: 2.1px;
  }

  .companions-value__value {
    align-self: flex-start;
  }
}
