.companions-filter {
  padding-top: 24px;

  background-color: inherit;
}

.companions-filter__wrapper {
  box-sizing: border-box;
  width: 84%;
  margin-left: auto;
  margin-right: auto;
  padding-left: 7.6%;
  padding-right: 5.2%;
  padding-top: 26px;
  padding-bottom: 22px;

  background-color: @fillter-companions-bg;
  border-radius: 20px;
}

.companions-filter__header {
  .indents-reset;
  width: 55%;
  margin-bottom: 22px;

  .bold-font;
  font-size: 22.5px;
  line-height: 22.5px;
  color: @companions-filter-title;

  word-break: break-all;
}

.companions-filter__form {
  width: 98%;
}

.companions-filter__title {
  width: 90%;
  margin: 0;
  padding-left: 1%;
  padding-top: 10px;
  padding-bottom: 7px;

  .bold-font;
  font-size: 16px;
  line-height: 22.5px;
  color: @companions-filter-title;

  word-break: break-all;

  text-transform: uppercase;

  &--transportation {
    padding-bottom: 10px;
  }

  & span {
    display: none;
  }
}

.companions-filter__item {
  margin: 0;
  padding: 0;
  position: relative;

  border: none;

  &:last-of-type {
    margin-bottom: 26px;
  }
}

.companions-filter__filter-category {
  position: relative;
  display: flex;

  border-top: 1px solid @filter-border-color;

  cursor: pointer;

  svg {
    position: absolute;
    right: 2px;
    top: 18px;
    width: 8px;
    height: 4px;
  }

  &:hover {
    .companions-filter__title {
      color: @companions-filter-title-hover;
    }

    svg {
      fill: @main-dark-color-opac-60;
    }
  }

  &:active {
    .companions-filter__title {
      color: @companions-filter-title-active;
    }

    svg {
      fill: @main-dark-color-opac-30;
    }
  }
}

.companions-filter__show-check-boxs {
  .companions-filter-button;
}


.companions-filter__check {
  &:focus ~ .companions-filter__filter-category {
    outline: rgb(59, 153, 252) auto 5px;
  }

  &:checked ~ .companions-filter__checkbox-wrapper {
    display: block;
  }

  &:checked ~ .companions-filter__filter-category {
    svg {
      transform: rotate(180deg);
    }
  }

  & ~ .companions-filter__checkbox-wrapper {
    display: none;
  }

  & ~ .companions-filter__level-range-wrapper {
    display: none;
  }

  &:checked ~ .companions-filter__level-range-wrapper {
    display: block;
  }

  &[name="show-transportation-block"]:checked ~ .companions-filter__checkbox-wrapper {
    display: flex;
  }
}

.companions-filter__checkbox-wrapper {
  margin-bottom: 16px;

  &--transportation {
    margin-bottom: 21px;
    padding-top: 2px;
  }
}

.companions-filter__show-button {
  box-sizing: border-box;
  display: block;
  width: 100%;
  padding-top: 7px;
  padding-bottom: 5px;
  padding-left: 2px;

  .bold-font;
  font-size: 17px;
  line-height: 30px;
  color: @secondary-color-font-button;

  word-break: break-all;

  text-transform: uppercase;

  background-color: @clear-bg;
  border: none;
  border-radius: 20px;


  cursor: pointer;

  &:hover {
    box-shadow: 0 2px 10px 0 rgba(0, 0, 0, 0.08);
  }

  &:active {
    color: @font-color-value;

    background-color: @active-filter-button-bg;
  }
}

@media (min-width: @tablet-width) {
  .companions-filter {
    padding-top: 46px;
    margin-bottom: 40px;
  }

  .companions-filter__wrapper {
    width: 89%;
    padding-left: 4%;
    padding-right: 2.2%;
    padding-top: 35px;
    padding-bottom: 38px;
  }

  .companions-filter__form {
    width: 100%;
    padding-top: 32px;

    border-top: 1px solid @filter-border-color;
  }

  .companions-filter__filter-category {
    width: 105px;
    border: none;

    &--transportation {
      display: flex;
    }
  }

  .companions-filter__header {
    width: 81%;
    margin-bottom: 38px;

    font-size: 30px;
    line-height: 30px;
  }

  .companions-filter__title {
    width: auto;
    margin-bottom: 17px;
    padding: 0;

    .medium-font;
    font-size: 20px;
    line-height: 30px;

    border-top: none;

    span {
      display: inline-block;
    }

    &--transportation {
      padding-top: 8px;
    }

    &--level {
      padding-top: 11px;
    }
  }

  .companions-filter__show-check-boxs {
    display: none;
  }

  .companions-filter__check {
    & ~ .companions-filter__checkbox-wrapper {
      display: flex;

      &--transportation {
        display: flex;
      }
    }

    &:checked ~ .companions-filter__level-range-wrapper {
      display: flex;
    }
  }

  .companions-filter__item {
    &:last-of-type {
      margin-bottom: 0;
    }

    &--food {
      margin-bottom: 0;
    }

    &--level {
      .companions-filter__level-range-wrapper {
        margin-left: auto;
        display: flex;
        flex-grow: 1;
      }
    }
  }

  .companions-filter__fieldset-container {
    display: flex;
  }

  .companions-filter__checkbox-wrapper {
    width: 79.5%;
    margin-bottom: 15px;
    margin-left: 3%;
    flex-wrap: wrap;

    /*&--hobbies {
      display: flex;
      margin-left: %;
    }*/

    &--music {
      display: flex;
    }

    &--food {
      display: flex;
    }

    &--transportation {

      margin-bottom: 0;
      padding-top: 0;
      padding-left: 0;
      display: flex;
    }
  }

  .companions-filter__label-check {
    display: block;

    font-size: 20px;
    line-height: 20px;
  }

  .companions-filter__show-button {
    position: relative;
    width: 97%;
    height: 50px;
    margin-top: 53px;
    margin-left: 0;
    margin-right: auto;

    font-size: 20px;
    line-height: 30px;

    border-radius: 25px;

    &::before {
      content: "";
      position: absolute;
      top: -38px;
      left: 0;
      width: 100%;
      height: 1px;

      background-color: @filter-border-color;
    }
  }
}

@media (min-width: @desktop-width) {
  .companions-filter {
    width: 21.5%;
    padding-top: 0;
  }

  .companions-filter__wrapper {
    width: 100%;
    padding-top: 47px;
    padding-bottom: 46px;
    padding-left: 16.3%;
    padding-right: 12.5%;
  }

  .companions-filter__header {
    margin-bottom: 31px;
  }

  .companions-filter__show-check-boxs {
    display: block;
  }

  .companions-filter__filter-category svg {
    right: 9px;
    top: 9px;
    width: 10px;
    height: 7px;
  }

  .companions-filter__filter-category {
    width: 100%;

    &--transportation {
      display: flex;
    }

    .companions-filter__show-check-boxs--level {
      top: 19px;
    }
  }

  .companions-filter__check[name="show-transportation-block"]:checked ~ .companions-filter__checkbox-wrapper {
    display: flex;
  }

  .companions-filter__check[name="show-transportation-block"] ~ .companions-filter__checkbox-wrapper {
    display: none;
  }

  .companions-filter__check {
    & ~ .companions-filter__checkbox-wrapper {
      display: none;
    }

    & ~ .companions-filter__checkbox-wrapper--transportation {
      display: none;
    }

    & ~ .companions-filter__level-range-wrapper {
      display: none;
    }

    &:checked ~ .companions-filter__level-range-wrapper {
      display: block;
    }
  }

  .companions-filter__form {
    padding-top: 0;

    border: none;
  }

  .companions-filter__title {
    margin-right: auto;
    margin-bottom: 0;

    span {
      display: none;
    }

    &--transportation {
      padding-top: 2px;
    }

    &--level {
      padding-top: 9px;
    }
  }

  .companions-filter__fieldset-container {
    display: block;
  }

  .companions-filter__checkbox-wrapper {
    width: 90.5%;
    margin: 0;
    padding-top: 19px;
  }

  .companions-filter__item {
    &:not(:last-of-type) {
      margin-bottom: 11px;
    }

    &--food {
      margin-bottom: 0;
    }

    &--transportation {
      .companions-filter__checkbox-wrapper--transportation {
        margin-bottom: 10px;
        padding-top: 13px;
        padding-left: 0;
        display: flex;
      }
    }

    &--level {
      .companions-filter__level-range-wrapper {
        padding-top: 12px;
      }
    }
  }

  .companions-filter__show-button {
    margin-top: 43px;

    &::before {
      content: none;
    }
  }
}
