.close-filter-button {
  display: none;
  width: 14px;
  height: 14px;
  padding: 0;

  background-color: transparent;
  border: none;
  background-image: url("../img/close.svg");
  background-repeat: no-repeat;

  & span {
    display: none;
  }
}

@media (min-width: @tablet-width) {
  .close-filter-button {
    height: 20px;
    width: 29%;
    padding-right: 29%;

    font-size: 20px;
    line-height: 24px;
    color: @main-dark-color;
    text-transform: uppercase;

    background-position: right;

    & span {
      display: block;
    }
  }
}

@media (min-width: @desktop-width) {
  .close-filter-button {
    height: 20px;
    width: 13%;
    padding: 0;

    font-size: 20px;
    line-height: 24px;
    color: @main-dark-color;
    text-transform: uppercase;

    & span {
      display: block;
    }
  }
}
