.flags {
  padding: 0;

  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;

  list-style: none;

  &--demin {
    width: 32%;
  }
}

.flags__item {
  position: relative;
  width: 35px;
  height: 24px;

  border-radius: 5px;

  background-size: contain;

  &--user-cart {
    position: relative;

    cursor: pointer;

    &::after {
      box-sizing: border-box;
      content: attr(data-hover);
      position: absolute;
      left: -100%;
      width: 130px;
      top: 30px;
      z-index: 1;
      display: inline-block;
      padding-top: 10px;
      padding-bottom: 10px;
      padding-left: 10px;
      padding-right: 10px;
      vertical-align: middle;

      .regular-font;
      font-size: 20px;
      line-height: 24px;
      color: @clear-bg;

      text-align: center;

      background-color: @hover-tooltype-bg;

      border-radius: 5px;

      opacity: 0;
      visibility: hidden;
    }

    &::before {
      content: "";
      position: absolute;
      top: 20px;
      width: 0;
      height: 0;
      border-left: 9px solid transparent;
      border-right: 9px solid transparent;
      border-bottom: 10px solid @hover-tooltype-bg;

      opacity: 0;
      visibility: hidden;
    }

    &:hover::after {
      opacity: 1;
      visibility: visible;
    }

    &:hover::before {
      opacity: 1;
      visibility: visible;
    }
  }

  &--sri-lanka {
    background-image: url("../img/flag-sri-lanka.svg");
  }

  &--thailand {
    background-image: url("../img/flag-thailand.svg");
  }

  &--seychelles {
    background-image: url("../img/flag-seychelles.svg");
  }

  &--belgium {
    background-image: url("../img/flag-belgium.svg");
  }

  &--czech {
    background-image: url("../img/flag-czech.svg");
  }
}

@media (min-width: @tablet-width) {
  .flags {
    width: 50%;

    &--demin {
      justify-content: flex-start;

      .flags__item {
        margin-right: 12%;
      }
    }
  }
}
