.plans-country {
  .list-res;
}

.plans-country__item {
  box-sizing: border-box;
  padding-top: 24px;
  padding-bottom: 22px;
  padding-left: 8.5%;

  background-color: @clear-bg;
  border-radius: 10px;

  &:not(:last-of-type) {
    margin-bottom: 9px;
  }
}

.plans-country__title {
  .indents-reset;
  width: 70%;
  margin-bottom: 20px;

  .bold-font;
  font-size: 16px;
  line-height: 16px;
  color: @primary-dark-font-color;

  word-break: break-all;
}

.plans-country__number {
  margin-right: 2px;

  letter-spacing: 1.6px;
}

.plans-country__companions-value {
  .indents-reset;
  width: 70%;
  margin-bottom: 5px;

  .medium-font;
  font-size: 14px;
  line-height: 14px;
  letter-spacing: 1.4px;
  color: @primary-dark-font-color;

  word-break: break-all;

  text-transform: uppercase;
}

.plans-country__dates {
  width: 80%;
  .indents-reset;
  margin-bottom: 22px;

  .medium-font;
  font-size: 14px;
  line-height: 14px;
  letter-spacing: 1.4px;
  color: @primary-dark-font-color;

  text-transform: uppercase;

  word-break: break-all;
}

.plans-country__change-button {
  width: 50%;
  display: block;
  padding: 0;

  .medium-font;
  font-size: 14px;
  line-height: 18px;
  letter-spacing: 1.4px;
  color: @font-color-value;

  text-transform: uppercase;

  border: none;
  background-color: transparent;

  cursor: pointer;

  word-break: break-all;

  &:hover {
    color: @font-color-value-30proc-up;
  }

  &:active {
    color: @font-color-value-20proc-down;
  }
}

@media (min-width: @tablet-width) {
  .plans-country__item {
    padding: 0;
    padding-top: 28px;
    padding-bottom: 16px;
    display: flex;
    flex-wrap: wrap;
  }

  .plans-country__title {
    box-sizing: border-box;
    width: 100%;
    margin: 0;
    margin-bottom: 20px;
    padding-bottom: 19px;
    padding-left: 6%;
    display: flex;
    flex-direction: row-reverse;
    justify-content: flex-end;

    font-size: 24px;
    line-height: 50px;

    border-bottom: 1px solid @form-border-second-value;
  }

  .plans-country__number {
    margin-left: auto;
    margin-right: 5%;
    align-self: center;

    font-size: 20px;
    line-height: 18px;
  }

  .plans-country__companions-value {
    width: 22%;
    margin: 0;
    margin-left: 5.6%;
    margin-right: auto;

    font-size: 20px;
    line-height: 18px;
  }

  .plans-country__dates {
    width: 28%;
    margin: 0;
    margin-right: 14%;

    font-size: 20px;
    line-height: 18px;
  }

  .plans-country__change-button {
    width: 17%;
    margin-right: 6%;

    font-size: 20px;
    line-height: 18px;
  }
}

@media (min-width: @desktop-width) {
  .plans-country__item {
    padding-top: 25px;
    padding-bottom: 22px;
    padding-left: 3.2%;
    padding-right: 2.7%;

    &:not(:last-of-type) {
      margin-bottom: 15px;
    }
  }

  .plans-country__title {
    width: 41%;
    padding: 0;
    margin: 0;
    flex-direction: row;
    justify-content: flex-start;

    border: none;
  }

  .plans-country__number {
    margin: 0;
    margin-right: 7%;

    .medium-font;
  }

  .plans-country__companions-value {
    width: 12%;
    margin-left: 8.5%;
    margin-right: 3.4%;
    align-self: center;
  }

  .plans-country__dates {
    width: 15%;
    margin-left: 0;
    margin-right: 10.7%;
    align-self: center;
  }

  .plans-country__change-button {
    width: 9%;
    margin: 0;
    align-self: center;
  }
}
