.companions-catalog {
  position: relative;

  background-color: @light-bg;
}

.companions-catalog__container {
  margin-bottom: 20px;
}

.companions-catalog__filter {
  margin-bottom: 27px;
}

.companions-catalog__to-all {
  box-sizing: border-box;
  position: relative;
  display: block;
  width: 84.3%;
  margin-left: auto;
  margin-right: auto;
  margin-bottom: 19px;
  padding-left: 31px;

  .bold-font;
  font-size: 17px;
  line-height: 30px;
  color: @font-yellow-button;

  text-transform: uppercase;
  text-align: center;

  .companions-catalog__to-all-icon {
    position: absolute;
    top: 4px;
    left: 22.5%;
    width: 18px;
    height: 18px;

    fill: @primary-dark-font-color;
  }

  &:hover {
    color: @primary-dark-font-color-opac-60;

    .companions-catalog__to-all-icon {
      fill: @primary-dark-font-color-opac-60;
    }
  }

  &:active {
    color: @primary-dark-font-active;

    .companions-catalog__to-all-icon {
      fill: @primary-dark-font-active;
    }
  }
}

.companions-catalog__pagination {
  margin-bottom: 46px;
}

@media (min-width: @tablet-width) {
  .companions-catalog__filter {
    margin-left: auto;
    margin-bottom: 40px;
  }

  .companions-catalog__to-all {
    margin-bottom: 48px;
    padding-left: 41px;

    font-size: 20px;
    line-height: 30px;

    .companions-catalog__to-all-icon {
      top: -1px;
      left: 35%;
      width: 27px;
      height: 27px;
    }
  }

  .companions-catalog__pagination {
    margin-bottom: 63px;
  }
}

@media (min-width: @desktop-width) {
  .companions-catalog__wrapper {
    width: 91.4%;
    margin-left: auto;
    margin-right: auto;
    max-width: 1462px;
  }

  .companions-catalog__container {
    margin-bottom: 66px;
    margin-left: auto;
    margin-right: auto;
    display: flex;
  }

  .companions-catalog__travelmates {
    order: -1;
  }

  .companions-catalog__to-all {
    width: 53%;
    margin-left: 113px;
    margin-bottom: 65px;
    padding: 0;

    .companions-catalog__to-all-icon {
      left: 33%;
    }
  }

  .companions-catalog__pagination {
    margin-bottom: 77px;
  }
}
