.check__label {
  position: relative;
  display: inline-block;
  width: 70%;
  margin: 0;
  margin-right: auto;
  padding-top: 2px;
  padding-left: 3px;

  .regular-font;
  font-size: 15px;
  line-height: 15px;
  color: inherit;

  cursor: pointer;

  word-break: break-all;

  &:not(:last-of-type) {
    margin-bottom: 8px;
  }

  &--steps {
    padding-left: 7%;
  }

  &:hover {
    color: @font-color-value-30proc-up;

    .check__box {
      background-color: @clear-bg-opac-60;
    }
  }

  &:active {
    color: @font-color-value;

    .check__box {
      background-color: @clear-bg-opac-30;

      &::after {
        background-color: @main-dark-color-opac-20;
      }
    }
  }
}

.check__box {
  box-sizing: border-box;
  position: absolute;
  left: 2px;
  top: 3px;
  display: inline-block;

  width: 18px;
  height: 18px;

  border-radius: 4px;
  background-color: @clear-bg;

  &::after {
    content: none;
    left: 4px;
    top: 7px;
    position: absolute;
    display: inline-block;

    width: 7px;
    height: 3px;

    border-left: 2px solid @main-dark-color;
    border-bottom: 2px solid @main-dark-color;

    transform: rotate(-45deg);
  }

  &--steps {
    left: 0;
    top: 0;
    width: 26px;
    height: 26px;
    margin-right: 1%;

    border: 1px solid @border-color;

    &::after {
      content: none;
      left: 7px;
      top: 8px;
      position: absolute;
      display: inline-block;

      width: 8px;
      height: 3px;

      border-left: 2px solid @main-dark-color;
      border-bottom: 2px solid @main-dark-color;

      transform: rotate(-45deg);
    }

    &:hover {
      border-color: @level-range-border-hover;
    }

    &:active {
      border: none;
      background-color: @activ-icon-check-bg;

      &::after {
        border-left: 2px solid @active-check-font;
        border-bottom: 2px solid @active-check-font;
      }
    }
  }
}

.check__input {
  opacity: 0;

  &:checked ~ .check__box::after {
    content: "";
  }

  &:focus ~ .check__box {
    outline: rgb(59, 153, 252) auto 5px;
  }
}


@media (min-width: @tablet-width) {
  .check__label {
    box-sizing: border-box;
    width: 30.8%;
    padding-left: 2.2%;
    padding-top: 6px;

    font-size: 20px;
    line-height: 20px;

    &--steps {
      width: 50%;
      padding-top: 11px;
      padding-left: 6%;

      font-size: 20px;
      line-height: 18px;
      letter-spacing: 2px;
      color: @primary-dark-font-color;

      text-transform: uppercase;

      .check__box {
        left: -3px;
        top: 4px;
        width: 36px;
        height: 36px;

        &::after {
          top: 12px;
          left: 11px;
          width: 12px;
          height: 5px;
        }
      }
    }

    &--lounge {
      padding-left: 3.2%;

      .check__box {
        left: 5px;
      }
    }

    &--hard-rock {
      margin-right: 2%;
    }

    &--russian-rap {
      margin-right: 3.8%;
    }

    &--fit-food {
      margin-right: 4.2%;
    }

    &--vegan {
      margin-right: 0;
      padding-left: 0.9%;

      .check__box {
        left: -6px;
      }
    }
  }

  .check__box {
    left: 0;
    top: 3px;
    width: 24px;
    height: 24px;
    margin-left: 1%;

    &::after {
      top: 7px;
      left: 6px;
      width: 11px;
      height: 5px;
    }
  }
}

@media (min-width: @desktop-width) {
  .check__label {
    display: block;
    width: 90%;
    padding-left: 6%;
    padding-top: 2px;

    &:not(:last-of-type) {
      margin-bottom: 17px;
    }

    &:last-of-type {
      margin-bottom: 17px;
    }

    &--steps {
      width: 98%;
      padding-top: 11px;
      padding-left: 55px;

      .check__box {
        margin-left: 28px;
      }
    }

    &--vegan {
      .check__box {
        left: 0;
      }
    }

    &--lounge {
      .check__box {
        left: 0;
      }
    }
  }

  .check__box {
    top: 0;

    &::after {
      top: 6px;
    }
  }
}
