.features {
  position: relative;
  z-index: 2;
  width: 100%;
}

.features__list {
  box-sizing: border-box;
  .list-res;
}

.features__description {
  .indents-reset;
  width: 58%;

  .bold-font;
  font-size: 18px;
  line-height: 18px;
  color: @secondary-color-font-button;
}

.features__item {
  box-sizing: border-box;
  position: relative;
  padding-top: 22px;
  padding-left: 9%;

  border-radius: 10px;
  border: none;

  &:not(:last-of-type) {
    padding-bottom: 44px;
  }

  &:last-of-type {
    padding-bottom: 22px;
  }

  &--one {
    background-color: @features-color-one;

    &::after {
      .features__item-auto;
      content: "1";
    }
  }

  &--two {
    background-color: @features-color-two;

    &::after {
      .features__item-auto;
      content: "2";
    }
  }

  &--three {
    background-color: @features-color-three;

    &::after {
      .features__item-auto;
      content: "3";
    }
  }

  &--four {
    background-color: @features-color-four;

    &::after {
      .features__item-auto;
      content: "4";
    }
  }

  &:not(:first-of-type) {
    margin-top: -22px;
  }
}

@media (min-width: @tablet-width) {
  .features {
    width: 99.5%;
  }

  .features__list {
    display: flex;
    flex-wrap: wrap;
  }

  .features__item {
    padding: 0;
    padding-left: 5%;

    &:not(:first-of-type) {
      margin-top: 0;
    }

    &:not(:last-of-type) {
      padding-bottom: 62px;
    }

    &::after {
      font-size: 30px;
      line-height: 120px;
    }

    &--one,
    &--two {
      padding-top: 30px;

      border-radius: 0;
      border-top-left-radius: 20px;
      border-top-right-radius: 20px;
    }

    &--three,
    &--four {
      padding-top: 30px;

      border-radius: 0;
      border-bottom-left-radius: 20px;
      border-bottom-right-radius: 20px;
    }

    &:nth-of-type(2n+1) {
      width: 58%;

      &::after {
        top: 43px;
        right: 89%;
      }
    }

    &:nth-of-type(2n) {
      width: 50%;
      margin-left: -8%;

      &::after {
        top: 43px;
        right: 87%;
      }
    }
  }

  .features__description {
    width: 51%;
    font-size: 24px;
    line-height: 24px;
  }
}

@media (min-width: @desktop-width) {
  .features {
    width: 100%;
  }

  .features__list {
    flex-direction: row;
  }

  .features__item {
    margin: 0;
    padding: 0;
    padding-top: 29px;
    padding-left: 2.8%;
    border-radius: 20px;

    &:not(:last-of-type) {
      width: 34.9%;
      padding-bottom: 29px;
    }

    &:nth-of-type(2n) {
      margin-left: -11%;
      padding-left: 2.4%;
    }

    &:last-of-type {
      width: 24%;
      margin-left: -9.8%;
    }

    &:not(:first-of-type):nth-of-type(2n+1) {
      width: 35.9%;
      margin-left: -9%;
      padding-left: 2.5%;
    }

    &::after {
      font-size: 24px;
      line-height: 120px;
    }
  }

  .features__item:first-of-type {
    &::after {
      top: -19px;
      right: 184px;
    }
  }

  .features__item:nth-of-type(2) {
    &::after {
      top: -19px;
      right: 154px;
    }
  }

  .features__item:nth-of-type(3) {
    &::after {
      top: -19px;
      right: 163px;
    }
  }

  .features__item:nth-of-type(4) {
    &::after {
      top: -19px;
      right: 34px;
    }
  }

  .features__description {
    width: 43%;
  }
}
