.add-photo-button {
  box-sizing: border-box;
  display: block;
  width: 100%;
  padding-top: 7px;
  padding-bottom: 3px;
  padding-left: 5%;
  padding-right: 5%;

  .bold-font;
  font-size: 17px;
  line-height: 30px;
  color: @dark-font-nav;

  text-transform: uppercase;

  word-break: break-all;

  background-color: @third-background;
  border-radius: 20px;
  border: none;

  cursor: pointer;
}

@media (min-width: @tablet-width) {
  .add-photo-button {
    padding: 0;
    padding-top: 23px;
    padding-bottom: 21px;

    .medium-font;
    font-size: 20px;
    line-height: 18px;
    letter-spacing: 2.1px;

    text-align: center;
  }
}

@media (min-width: @desktop-width) {
  .add-photo-button {
    width: 191px;
    padding-top: 15px;
    padding-bottom: 14px;
    padding-left: 5px;

    border-radius: 0;
    border-bottom-left-radius: 10px;
    border-bottom-right-radius: 10px;

    span {
      display: none;
    }

    &:hover {
      background-color: @photo-btn-bg-hover;
    }

    &:active {
      color: @font-color-value;
    }
  }
}
