.page-header {
  box-sizing: border-box;
  position: relative;

  background: url("../img/traveller-illustration-mobile@1x.png"), url("../img/globe-back-mobile@1x.jpg");
  background-color: @header-bg;
  background-repeat: no-repeat;
  background-size: 192px 255px, 100% 569px;
  background-position: 79px 223px, top;

  @media (min-resolution: @retina-dpi), (min-resolution: @retina-dppx) {
    background-image: url("../img/traveller-illustration-mobile@2x.png"), url("../img/globe-back-mobile@2x.jpg");
  }

  &--catalog {
    background: none;
    background-color: @dark-font-nav;

    @media (min-resolution: @retina-dpi), (min-resolution: @retina-dppx) {
      background: none;
      background-color: @dark-font-nav;
    }
  }
}

.page-header__wrapper {
  box-sizing: border-box;
  width: 84%;
  margin-left: auto;
  margin-right: auto;

  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;

  &--opened-menu {
    padding-top: 93px;
  }
}

.page-header__logo {
  display: block;
  margin-bottom: 48px;

  &--catalog {
    margin-bottom: 55px;
  }

  &--form {
    margin-bottom: 55px;
  }
}

.page-header__logo-image {
  display: block;
  width: 100px;
  height: 19px;
}

.page-header__title {
  .indents-reset;
  width: 89%;
  margin-left: auto;
  margin-right: auto;
  margin-bottom: 244px;

  word-wrap: break-word;

  .page-title;

  text-align: center;
}

.page-header__review {
  margin-bottom: 29px;
  padding-left: 9%;
}

.page-header__user-nav-list {
  position: relative;
  z-index: 2;
  width: 100%;
  margin-top: 0;
  margin-left: auto;
  margin-right: auto;
  margin-bottom: 27px;
}

@media (min-width: @tablet-width) {
  .page-header {
    padding: 0;

    background: url("../img/traveller-illustration-tablet@1x.png"), url("../img/globe-back-tablet@1x.jpg");
    background-color: @header-bg;
    background-size: 372px 451px, 1479px 612px;
    background-position: 284px 403px, -356px 382px;
    background-repeat: no-repeat;

    @media (min-resolution: @retina-dppx), (min-resolution: @retina-dpi) {
      background: url("../img/traveller-illustration-tablet@2x.png"), url("../img/globe-back-tablet@2x.jpg");
      background-color: @header-bg;
      background-size: 372px 451px, 1479px 612px;
      background-position: 284px 403px, -356px 382px;
      background-repeat: no-repeat;
    }

    &--index {
      &::after {
        content: none;
      }
    }

    &--catalog {
      background: none;
      background-color: @dark-font-nav;

      @media (min-resolution: @retina-dpi), (min-resolution: @retina-dppx) {
        background: none;
        background-color: @dark-font-nav;
      }
    }

    &--form {
      background: none;
      background-color: @dark-font-nav;

      @media (min-resolution: @retina-dpi), (min-resolution: @retina-dppx) {
        background: none;
        background-color: @dark-font-nav;
      }
    }
  }

  .page-header__wrapper {
    margin-top: -63px;
    width: 89.9%;

    &--opened-menu {
      padding-top: 227px;
    }

    &--catalog {
      padding-top: 40px;
      width: 90%;
    }

    &--form {
      padding-top: 40px;
    }
  }

  .page-header__logo {
    height: 42px;
    width: 166px;
    padding-top: 5px;
    margin: 0;
    order: -4;
  }

  .page-header__logo-image {
    width: 166px;
    height: 42px;
  }

  .page-header__button {
    margin-top: 21px;
    margin-left: 10%;
    order: -3;

    &--catalog {
      margin-top: 21px;
      margin-left: 9.6%;
    }

    &--form {
      margin-top: 21px;
    }
  }

  .page-header__user-nav-list {
    display: none;
  }

  .page-header__title {
    width: 63%;
    margin-left: auto;
    margin-right: auto;
    margin-bottom: 85px;

    font-size: 60px;
    line-height: 70px;
    text-align: center;
  }

  .page-header__review {
    margin-right: auto;
    margin-bottom: 211px;
    padding-left: 5%;
  }
}


@media (min-width: @desktop-width) {
  .page-header {
    position: relative;
    z-index: 8;

    background: url("../img/traveller-illustration-desktop@1x.png"), url("../img/globe-back-desktop@1x.jpg");
    background-color: @header-bg-desktop;
    background-repeat: no-repeat;
    background-position: 60% 110px, 50% top;
    background-size: 431px 640px, 1800px 750px;

    @media (min-resolution: @retina-dppx), (min-resolution: @retina-dpi) {
      background: url("../img/traveller-illustration-desktop@2x.png"), url("../img/globe-back-desktop@2x.jpg");
      background-color: @header-bg-desktop;
      background-repeat: no-repeat;
      background-position: 60% 110px, 50% top;
      background-size: 431px 640px, 1800px 750px;
    }

    border-bottom-left-radius: 60px;
    border-bottom-right-radius: 60px;

    &--catalog {
      border-radius: 0;
      background: none;
      background-color: @dark-font-nav;

      @media (min-resolution: @retina-dpi), (min-resolution: @retina-dppx) {
        background: none;
        background-color: @dark-font-nav;
      }
    }

    &--form {
      z-index: 0;

      border-radius: 0;
      background: none;
      background-color: @dark-font-nav;

      @media (min-resolution: @retina-dpi), (min-resolution: @retina-dppx) {
        background: none;
        background-color: @dark-font-nav;
      }
    }
  }

  .page-header__wrapper {
    width: 92%;
    max-width: 1462px;
    margin-top: 0;
    padding-top: 39px;
    padding-bottom: 124px;

    &--catalog {
      padding-bottom: 120px;
      justify-content: flex-start;
    }

    &--form {
      padding-bottom: 120px;
    }

    &--scroll-menu {
      .page-header__user-nav-list {
        top: -109px;
      }
    }
  }

  .page-header__logo-image {
    width: 200px;
    height: 52px;
  }

  .page-header__button {
    display: none;
  }

  .page-header__nav {
    display: block;
    width: 40%;
    order: -2;
  }

  .page-header__user-nav-list {
    position: relative;
    top: -162px;
    right: -32%;
    width: 21%;
    margin: 0;
    align-self: flex-start;

    &--form {
      top: -100px;
      right: -73.5%;
    }
  }

  .page-header__title {
    width: 30%;
    margin: 0;
    margin-left: 70px;
    margin-bottom: 100px;
    padding: 0;
    text-align: left;
  }

  .page-header__review {
    padding: 0;
    margin-right: 2%;
    padding-top: 9px;
    margin-bottom: 0;
  }

  .page-header__features {
    position: relative;
    margin: 0;
    top: 0;
    width: 1320px;
  }
}
