.visually-hidden() {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.indents-reset() {
  margin: 0;
  padding: 0;
}

.list-res() {
  margin: 0;
  padding: 0;

  list-style: none;
}

.features__item-auto() {
  position: absolute;
  top: 23px;
  right: 8%;
  width: 9px;
  height: 12px;

  .bold-font;
  font-size: 18px;
  line-height: 18px;
  color: @clear-font;
}

.countries__description() {
  .medium-font;
  color: rgba(68, 68, 68, 0.3);
}

.page-title() {
  font-family: "Bloggersans", "Arial", sans-serif;
  font-size: 36px;
  line-height: 40px;
  font-style: normal;
  font-weight: 700;
  color: @clear-font;
}

.page-header__button-pseudo() {
  position: absolute;
  left: 0;
  width: 25px;
  height: 3px;

  content: "";

  background-color: @clear-bg;
  border-radius: 5px;
}

.fieldset-res() {
  margin: 0;
  padding: 0;

  border: none;
}

.button-actions() {
  &:hover {
    background-color: @yellow-button-hover-bg;
  }

  &:active {
    color: @font-yellow-button-active;

    svg {
      fill: @font-yellow-button-active;
    }
  }
}

.companions-filter-button() {
  svg {
    width: 8px;
    height: 4px;

    fill: @main-dark-color;
  }
}

.input-field-value-title() {
  .medium-font;
  line-height: 16px;

  text-transform: uppercase;
}

//-----------------Шрифты------------------//

.basic-font() {
  font-family: "Bloggersans", "Arial", sans-serif;
  font-size: 16px;
  line-height: 22px;
  font-weight: 300;
  font-style: normal;
  color: inherit;
}

.regular-font() {
  font-family: "Bloggersans", "Arial", sans-serif;
  font-weight: 400;
  font-style: normal;
}

.bold-font() {
  font-family: "Bloggersans", "Arial", sans-serif;
  font-weight: 700;
  font-style: normal;
}

.medium-font() {
  font-family: "Bloggersans", "Arial", sans-serif;
  font-weight: 500;
  font-style: normal;
}

.title-font() {
  font-family: "Bloggersans", "Arial", sans-serif;
  font-size: 24px;
  line-height: 24px;
  font-weight: 700;
  font-style: normal;
  word-wrap: break-word;
}
