.user-nav {
  padding: 0;
  display: flex;
  justify-content: center;
  flex-wrap: wrap;

  list-style: none;

  word-wrap: break-word;

  &--menu {
    padding: 0;
    width: 98%;

    list-style: none;
  }

  &--form,
  &--catalog {
    display: none;
  }
}

.user-nav__item {
  &--menu {
    width: 100%;
  }
}

.user-nav--catalog {
  display: none;
}

.user-nav__icon {
  fill: @dark-font-nav;
}

.user-nav__tel {
  box-sizing: border-box;
  display: block;
  width: 40px;
  padding-top: 11px;
  padding-bottom: 4px;
  padding-left: 12px;

  border-radius: 50%;
  background-color: @features-color-one;

  &:hover {
    background-color: @user-nav-phone-btn-bg;
  }

  &:active {
    .user-nav__icon {
      opacity: 0.3;
    }
  }
}

.user-nav__email {
  box-sizing: border-box;
  display: block;
  width: 40px;
  padding-top: 11px;
  padding-bottom: 4px;
  padding-left: 12px;

  border-radius: 50%;
  background-color: @features-color-three;

  &:hover {
    background-color: @user-nav-mail-btn-bg;
  }

  &:active {
    .user-nav__icon {
      opacity: 0.3;
    }
  }
}

.user-nav__login {
  box-sizing: border-box;
  display: block;
  width: 188px;
  padding-top: 8px;
  padding-bottom: 4px;
  padding-left: 38px;

  .bold-font;
  font-size: 17px;
  line-height: 30px;
  color: @dark-font-nav;

  background-color: @yellow-button-color;
  border-radius: 25px;

  text-transform: uppercase;

  &--menu {
    width: 100%;
    padding-left: 28.6%;
    padding-right: 8%;
    padding-top: 11px;
    padding-bottom: 10px;
  }

  &:hover {
    background-color: @yellow-button-hover-bg;
  }

  &:active {
    color: rgba(0, 0, 0, 0.3);
  }
}

@media (min-width: @tablet-width) {
  .user-nav {
    display: none;

    &--menu {
      width: 25.5%;
    }
  }

  .user-nav__login {
    padding-top: 13px;
    padding-left: 29.8px;
    padding-bottom: 8px;

    font-size: 20px;

    border-radius: 25px;
  }

  .user-nav--catalog {
    margin: 0;
  }

  .user-nav__tel {
    display: none;
  }

  .user-nav__email {
    display: none;
  }
}

@media (min-width: @desktop-width) {
  .user-nav {
    width: 22.5%;
    padding-right: 2px;
    display: flex;
    justify-content: flex-end;


    &--menu {
      display: none;
    }

    &--form,
    &--catalog {
      top: -100px;
      right: -73.4%;
      display: flex;
    }

    &--catalog {
      top: -100px;
      right: -65.4%;
      margin-left: 8%;
    }
  }

  .user-nav__item {
    .user-nav__login {
      padding-left: 27.8px;
    }
  }

  .user-nav__login {
    &--user-nav {
      display: none;
    }
  }

  .user-nav__tel {
    width: 50px;
    height: 50px;
    padding-top: 15px;
    padding-left: 15px;
    display: block;
  }

  .user-nav__email {
    width: 50px;
    height: 50px;
    padding-top: 15px;
    padding-left: 15px;
    display: block;
  }
}
