.hashtags-area {
  position: relative;
  width: 100%;
}

.hashtags-area__title {
  .indents-reset;
  position: relative;
  right: 39%;
  width: 26%;
  top: 10px;
  left: 37.2%;

  .regular-font;
  line-height: 18px;
  color: @primary-dark-font-color;

  text-align: center;

  word-break: break-all;

  background-color: @form-bg;
}

.hashtags-area__textarea {
  width: 100%;
  min-height: 89px;
  box-sizing: border-box;
  padding-top: 22px;
  padding-bottom: 20px;
  padding-left: 9.5%;
  padding-right: 9.5%;

  .regular-font;
  font-size: 16px;
  line-height: 22px;

  background-color: transparent;
  border: 1px solid @border-color;
  border-radius: 10px;

  resize: none;

  &::placeholder {
    .regular-font;
    font-size: 20px;
    line-height: 24px;
  }

  &:hover {
    border-color: @border-hover-color;
  }

  &:focus {
    border-color: @border-color-active;
  }
}

@media (min-width: @tablet-width) {
  .hashtags-area {
    width: 99%;
    margin-left: auto;
    margin-right: auto;
  }

  .hashtags-area__title {
    width: 14%;
    top: 105px;
    left: 43%;

    .medium-font;
    font-size: 20px;
    line-height: 18px;
    letter-spacing: 2.1px;

    text-transform: uppercase;
  }

  .hashtags-area__textarea {
    padding-left: 6.3%;
    padding-top: 34px;
    padding-bottom: 11px;

    font-size: 20px;
    line-height: 24px;
  }
}

@media (min-width: @desktop-width) {
  .hashtags-area {
    width: 58.3%;
    margin-left: 0;
  }

  .hashtags-area__title {
    top: 87px;
    left: 4%;
    width: 10%;
  }

  .hashtags-area__textarea {
    margin-top: -18px;
    padding-left: 39px;
    padding-bottom: 10px;
  }
}
