.travel-direction {
  position: relative;
  top: -25px;

  background-color: @light-bg;
  border-bottom-left-radius: 30px;
  border-bottom-right-radius: 30px;
}

.travel-direction__wrapper {
  box-sizing: border-box;
  width: 84%;
  margin-left: auto;
  margin-right: auto;
  padding-top: 97px;
  padding-bottom: 30px;
}

.travel-direction__title {
  .indents-reset;
  margin-bottom: 19px;
  margin-left: 9.2%;

  .title-font;
  color: @index-section-title;
}

.travel-direction__description {
  .indents-reset;
  margin-left: 9%;
  margin-bottom: 60px;
  width: 72%;

  word-wrap: break-word;
}

.travel-direction__list {
  margin-bottom: 27px;
}

.travel-direction__to-all {
  box-sizing: border-box;
  display: block;
  width: 100%;
  padding-top: 22px;
  padding-bottom: 21px;
  padding-left: 8%;
  padding-right: 1px;

  .bold-font;
  font-size: 18px;
  line-height: 18px;
  color: @font-yellow-button;

  background-color: @yellow-button-color;
  border-radius: 10px;

  .travel-direction__to-all-icon {
    margin-left: 42%;
    fill: @font-yellow-button;
  }

  .button-actions;
}

@media (min-width: @tablet-width) {
  .travel-direction {
    border-bottom-left-radius: 60px;
    border-bottom-right-radius: 60px;

    background: url("../img/flags-back@1x.png");
    background-color: @light-bg;
    background-repeat: no-repeat;
    background-size: 289px 575px;
    background-position: 91.5% 105px;

    @media (min-resolution: @retina-dppx), (min-resolution: @retina-dpi) {
      background: url("../img/flags-back@2x.png");
      background-repeat: no-repeat;
      background-size: 289px 575px;
      background-position: 91.5% 105px;
    }

    &::before {
      content: "";
      position: absolute;
      top: 431px;
      left: 40px;
      z-index: -1;
      display: block;
      width: 141px;
      height: 141px;

      background-color: @bg-country-filter;
      border-radius: 17px;
    }
  }

  .travel-direction__wrapper {
    width: 90%;
    padding-top: 98px;
    padding-bottom: 79px;
  }

  .travel-direction__title {
    width: 40%;
    margin-left: 5%;
    margin-bottom: 56px;

    font-size: 45px;
    line-height: 45px;
  }

  .travel-direction__description {
    width: 49%;
    margin-left: 5.2%;
    margin-bottom: 106px;

    font-size: 28px;
    line-height: 33px;
  }

  .travel-direction__list {
    margin-bottom: 50px;
  }

  .travel-direction__to-all {
    width: 89.2%;
    margin-left: 5.1%;
    margin-right: auto;
    padding-left: 5%;
    padding-top: 30px;
    padding-bottom: 27px;

    font-size: 24px;
    line-height: 24px;

    background-position: 94% center;

    border-radius: 20px;

    .travel-direction__to-all-icon {
      margin-left: 67.5%;
    }
  }
}

@media (min-width: @desktop-width) {
  .travel-direction {
    min-height: 750px;

    background-color: @light-bg;
    background-position: 95% 112px;

    &::before {
      top: 112px;
      left: 60px;
    }
  }

  .travel-direction__wrapper {
    width: 92%;
    max-width: 1462px;
    padding: 0;
    padding-top: 186px;
    padding-bottom: 100px;
    display: flex;
    flex-wrap: wrap;
  }

  .travel-direction__container {
    width: 39.3%;
    margin-left: auto;
  }

  .travel-direction__title {
    width: 65%;
    margin: 0;
    margin-bottom: 56px;
    padding-top: 4px;
  }

  .travel-direction__description {
    width: 66%;
    margin: 0;
    padding-bottom: 129px;
  }

  .travel-direction__list {
    margin-top: 0;
    margin-left: 5.5%;
    order: -1;
  }

  .travel-direction__to-all {
    width: 20.5%;
    margin: 0;
    margin-left: 61%;
    margin-top: -120px;
    padding-left: 31px;
    align-self: flex-start;

    .travel-direction__to-all-icon {
      margin-left: 19.8%;
    }
  }
}
