.transportation {
  &--travelmates {
    position: relative;
    width: 68%;
  }
}

.transportation__title {
  display: block;
  .indents-reset;
  position: relative;
  top: -9px;
  left: 7%;
  width: 50%;

  .regular-font;
  font-size: 15px;
  line-height: 18px;

  word-break: break-all;

  text-align: center;

  background-color: @clear-bg;
}

@media (min-width: @tablet-width) {
  .transportation__title {
    display: none;
  }
}

@media (min-width: @desktop-width) {
  .transportation__title {
    display: none;
  }

  .transportation {
    &--travelmates {
      width: 57%;
      margin-right: 36px;
      padding-top: 10px;
    }
  }
}
