.profile-photo__avatar {
  width: 100px;
  height: 100px;
  border-radius: 50%;
}

.profile-photo__button {
  display: none;
}

@media (min-width: @tablet-width) {
  .profile-photo__avatar {
    width: 94px;
    height: 94px;

    border-radius: 5px;
  }
}

@media (min-width: @desktop-width) {
  .profile-photo__avatar {
    box-sizing: border-box;
    width: 190px;
    height: 190px;
    display: block;

    border-radius: 0;
    border-top-left-radius: 5px;
    border-top-right-radius: 5px;
  }

  .profile-photo__button {
    display: block;
    padding-top: 16px;
    padding-bottom: 14px;
  }
}
