.likes {
  width: 35%;
  display: flex;
  overflow: hidden;

  cursor: pointer;
}

.likes__img {
  box-sizing: border-box;
  width: 31px;
  height: 31px;
  margin-right: 6%;
  padding-top: 11px;
  padding-left: 10px;

  border-radius: 50%;
  border: none;
}

.likes__icon-active,
.likes__icon-no-active {
  width: 13px;
  height: 11px;
}

.likes__check {
  .visually-hidden;

  &:checked ~ .likes__img {
    .likes__icon-active {
      display: block;

      fill: @like-color;
    }

    .likes__icon-no-active {
      display: none;
    }

    background-color: @active-like-bg;

    &:hover {
      background-color: @active-like-bg-hover;
    }

    &:active {
      .likes__icon-active {
        opacity: 0.3;
      }
    }
  }


  & ~ .likes__img {
    .likes__icon-active {
      display: none;

      fill: @like-color;
    }

    .likes__icon-no-active {
      display: block;

      stroke: @no-active-like;
    }

    &:hover {
      background-color: @bg-country-filter;
    }

    &:active {
      .likes__icon-no-active {
        stroke: @no-active-like-act;
      }
    }
  }
}

.likes__value {
  .indents-reset;
  padding-top: 5px;
  align-self: center;

  .bold-font;
  font-size: 14px;
  line-height: 21px;
  color: @secondary-color-font-button;
}

@media (min-width: @tablet-width) {
  .likes {
    width: 20%;
  }

  .likes__icon-active,
  .likes__icon-no-active {
    width: 20px;
    height: 17px;
  }

  .likes__img {
    width: 50px;
    height: 50px;
    margin: 0;
    padding-top: 18px;
    padding-left: 15px;

    .likes__icon-active {
      width: 20px;
      height: 17px;
    }

    .likes__icon-no-active {
      width: 20px;
      height: 17px;
    }
  }

  .likes__value {
    margin-left: 4.9%;
    padding-top: 7px;

    font-size: 20px;
    line-height: 30px;

    &--demin {
      padding-top: 5px;
    }
  }

  .likes__heart {
    width: 20px;
    height: 17px;
  }
}

@media (min-width: @desktop-width) {
  .likes {
    width: 14%;
  }

  .likes__value {
    margin-left: 9px;
    padding-top: 5px;
  }
}
