.page-title-wrapper {
  box-sizing: border-box;
  position: relative;
  z-index: 3;
  padding-top: 8px;
  padding-bottom: 6px;

  background-color: @dark-font-nav;

  border-bottom-left-radius: 20px;
  border-bottom-right-radius: 20px;
}

.page-title-wrapper__wrapper {
  width: 84%;
  margin-left: auto;
  margin-right: auto;
}

.page-title-wrapper__title {
  position: relative;
  .indents-reset;
  width: 65%;

  .bold-font;
  font-size: 36px;
  line-height: 40px;
  color: @clear-font;

  word-break: break-all;

  &--form {
    width: 80.5%;
  }

  &::after {
    content: "";
    position: relative;
    top: 7px;
    display: block;
    width: 100%;
    height: 5px;

    background-color: @title-page-anderline-color;
  }
}

.page-title-wrapper__link-main-page {
  display: none;
}

@media (min-width: @tablet-width) {
  .page-title-wrapper {
    border-bottom-left-radius: 60px;
    border-bottom-right-radius: 60px;
  }

  .page-title-wrapper__wrapper {
    width: 89%;
    margin-left: auto;
    margin-right: auto;
    padding-top: 6px;
    padding-bottom: 70px;
    padding-right: 5%;
    display: flex;
  }

  .page-title-wrapper__title {
    width: 43%;
    height: 56px;
    margin-right: 22%;
    padding-left: 7.2%;

    font-size: 60px;
    line-height: 70px;

    &::after {
      top: 58px;
      height: 8px;

      background-color: @title-page-anderline-color;
      border-radius: 15px;
    }

    &--form {
      width: 53%;
      padding-left: 7.5%;
    }
  }

  .page-title-wrapper__link-main-page {
    display: block;
    width: 26%;
    padding-top: 25px;
    padding-right: 2%;

    .bold-font;
    font-size: 20px;
    line-height: 24px;
    color: @clear-font;
    text-transform: uppercase;

    .page-title-wrapper__link-main-page-icon {
      margin-left: 13%;

      fill: @clear-bg;
    }

    &:hover {
      color: @clear-bg-opac-60;

      .page-title-wrapper__link-main-page-icon {
        fill: @clear-bg-opac-60;
      }
    }

    &:active {
      color: @clear-bg-opac-30;

      .page-title-wrapper__link-main-page-icon {
        fill: @clear-bg-opac-30;
      }
    }

    &--form {
      display: none;
    }
  }
}

@media (min-width: @desktop-width) {
  .page-title-wrapper__wrapper {
    width: 91%;
    padding-top: 11px;
    padding-bottom: 70px;
    max-width: 1462px;
  }

  .page-title-wrapper__title {
    margin-right: 52%;
    padding-left: 7.5%;

    &::after {
      top: 58px;
      width: 285px;
    }

    &--form {
      &::after {
        width: 355px;
      }
    }
  }

  .page-title-wrapper__link-main-page {
    padding-top: 25px;
    padding-right: 0;
  }
}
