.countries {
  padding: 0;
  list-style: none;
}

.countries__item {
  box-sizing: border-box;
  position: relative;
  display: flex;
  flex-direction: column-reverse;

  &:not(:last-of-type) {
    margin-bottom: 10px;
  }

  &::before {
    content: "";
    position: absolute;
    top: 87px;
    left: 9%;
    z-index: 2;
    display: block;
    width: 35px;
    height: 24px;

    background-repeat: no-repeat;
    background-size: contain;
  }

  &--thailand::before {
    background-image: url("../img/flag-thailand.svg");
  }

  &--france::before {
    background-image: url("../img/flag-france.svg");
  }

  &--czech::before {
    background-image: url("../img/flag-czech.svg");
  }

  &:last-of-type {
    &::after {
      content: "";
      display: block;
      position: absolute;
      left: 0;
      bottom: -5px;
      z-index: 1;
      height: 81px;
      width: 100%;

      background-image: linear-gradient(to top, @light-bg, transparent);
      border-bottom-left-radius: 9px;
      border-bottom-right-radius: 9px;
    }
  }
}

.countries__img {
  width: 100%;
  height: 110px;
  border-radius: 9px;
}

.countries__description-wpapper {
  box-sizing: border-box;
  position: relative;
  z-index: 1;
  margin-top: -17px;
  padding-top: 23.5px;
  padding-bottom: 21.5px;
  padding-left: 9%;
  padding-right: 9%;
  display: flex;
  align-items: flex-start;
  flex-wrap: wrap;

  background-color: @clear-bg;
  border-radius: 9px;
}

.countries__title {
  min-width: 29%;
  margin: 0;
  margin-right: auto;
  padding-top: 3px;

  .bold-font;
  font-size: 21px;
  line-height: 21px;
  color: @secondary-color-font-button;

  word-break: break-all;
}

.countries__description {
  width: 81%;
  margin: 0;
  margin-top: -4px;

  .countries__description;
  line-height: 18px;
  color: @font-color-value;

  word-break: break-all;
}

.countries__followers {
  box-sizing: border-box;
  width: 28%;
  margin: 0;

  .medium-font;
  line-height: 30px;
  color: @font-color-value;

  text-align: right;

  word-break: break-all;

  .countries__followers-icon {
    position: relative;
    top: 0;
    left: -1px;
    width: 11px;
    height: 11px;

    fill: @main-dark-color-opac-10;
  }
}

@media (min-width: @tablet-width) {
  .countries__item {
    width: 89.4%;
    margin-left: 5.1%;
    margin-right: auto;
    position: relative;
    margin-bottom: 10px;
    display: flex;
    flex-direction: row;

    &::before {
      top: 17px;
      left: 64.5%;
      width: 70px;
      height: 47px;
    }
  }

  .countries__description-wpapper {
    position: relative;
    z-index: 0;
    bottom: 0;
    width: 100%;
    padding-top: 28px;
    padding-left: 5%;
    padding-bottom: 16px;
    padding-right: 0;
    margin: 0;
    flex-direction: column;
    flex-wrap: nowrap;

    border-radius: 20px;

    &--france {
      padding-top: 20px;
    }
  }

  .countries__title {
    width: 30%;
    margin-bottom: 9px;
    order: -2;

    font-size: 24px;
    line-height: 24px;
  }

  .countries__img {
    display: block;
    position: absolute;
    top: 0;
    right: 0;
    width: 30.1%;
    height: 140px;

    border-radius: 0;
    border-top-right-radius: 18px;
    border-bottom-right-radius: 18px;
  }

  .countries__description {
    width: 43%;
    order: -1;

    font-size: 20px;
    line-height: 17.7px;
    color: @font-color-value;
  }

  .countries__followers {
    width: 14%;
    margin-top: 16px;

    font-size: 20px;
    line-height: 30px;
    color: @font-color-value;

    &--chech {
      padding-right: 11px;
    }

    .countries__followers-icon {
      top: 3px;
      width: 20px;
      height: 20px;
      margin-right: 3%;
    }
  }
}

@media (min-width: @desktop-width) {
  .countries {
    width: 47%;
  }

  .countries__item {
    width: 89.8%;
    margin: 0;

    &::before {
      top: 22px;
      left: 60.5%;
    }

    &:not(:last-of-type) {
      margin-bottom: 10px;
    }
  }

  .countries__description-wpapper {
    padding-top: 24px;
    padding-bottom: 17px;
    padding-left: 5.4%;
  }

  .countries__title {
    margin-bottom: 9px;
    padding-top: 0;
  }

  .countries__followers {
    width: 16%;
    margin-top: 22px;

    .countries__followers-icon {
      top: 2px;
      left: -3px;
    }
  }

  .countries__img {
    width: 33.2%;
  }
}
