.hashtags {
  width: 221px;
  padding: 0;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  list-style: none;

  &--in-travelmates {
    width: 72%;
    justify-content: flex-start;
  }

  &--demin {
    width: 76%;
  }
}

.hashtags__item {
  .indents-reset;

  .regular-font;
  font-size: 15px;
  line-height: 18px;

  word-break: break-all;

  &:not(:last-of-type) {
    margin-right: 5px;
  }
}

@media (min-width: @tablet-width) {
  .hashtags {
    width: auto;

    &--in-travelmates {
      width: 34%;
    }
  }

  .hashtags__item {
    font-size: 20px;
    line-height: 24px;

    &:not(:last-of-type) {
      margin-right: 2%;
    }
  }
}

@media (min-width: @desktop-width) {
  .hashtags {
    &--in-travelmates {
      width: 78%;
    }
  }
}
