.add-country-button {
  width: 100%;
  padding: 0;
  padding-top: 14px;
  padding-bottom: 12px;
  padding-left: 12px;
  padding-right: 2%;

  .medium-font;
  font-size: 14px;
  line-height: 18px;
  letter-spacing: 1.3px;
  color: @primary-dark-font-color;

  word-break: break-all;

  text-transform: uppercase;
  text-align: left;

  background-color: transparent;
  border: none;
  border-radius: 5px;

  cursor: pointer;

  .add-country-button__icon {
    position: absolute;
    right: 15px;

    fill: @font-color-value;
  }

  &:hover {
    background-color: @available-days;
  }

  &:active {
    color: @font-color-value;

    background-color: @third-background;

    .add-country-button__icon {
      fill: @font-color-value-20proc-down;
    }
  }
}

@media (min-width: @tablet-width) {
  .add-country-button {
    width: 100%;
    padding-top: 27px;
    padding-bottom: 24px;
    padding-left: 7%;

    font-size: 20px;
    line-height: 18px;
  }

  .add-country-button__icon {
    top: 26px;
    right: 6%;
    width: 18px;
    height: 18px;
  }
}

@media (min-width: @desktop-width) {
  .add-country-button {
    padding-left: 27px;
    padding-top: 29px;

    .add-country-button__icon {
      right: 10px;
    }
  }
}
