.links-for-steps {
  box-sizing: border-box;
  width: 18%;
  padding: 0;
  padding-top: 8px;
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;

  list-style: none;

  &--in-step {
    display: none;
  }
}

.links-for-steps__item {
  position: relative;

  &::before {
    content: "";
    display: block;
    width: 5px;
    height: 5px;

    background-color: @primary-dark-font-active;
    border-radius: 50%;
  }

  &--active::after {
    content: "";
    position: absolute;
    top: 0;
    display: block;
    width: 5px;
    height: 5px;

    background-color: @primary-dark-font-color;
    border-radius: 50%;
  }

  & span {
    display: none;
  }
}

@media (min-width: @tablet-width) {
  .links-for-steps {
    width: 13%;
    padding: 0;
    align-self: center;
  }

  .links-for-steps__item {
    &:not(:last-of-type) {
      margin-right: 39%;
    }
  }
}

@media (min-width: @desktop-width) {
  .links-for-steps {
    width: 160px;
    display: flex;
    flex-direction: column;

    &--step1 {
      position: absolute;
      right: 180px;
      top: 257px;
    }
  }

  .links-for-steps__item {
    position: relative;
    width: 200px;

    &:not(:last-of-type) {
      margin-bottom: 22px;
    }

    span {
      display: inline-block;
      .regular-font;
      font-size: 20px;
      line-height: 14px;
      letter-spacing: 2px;
      color: @not-clear-font-60opac;

      text-transform: uppercase;
    }

    &--active {
      span {
        color: @not-clear-font;
      }

      &::after {
        left: -42px;
        top: 7px;
      }
    }

    &::before {
      position: absolute;
      left: -42px;
      top: 7px;
    }
  }
}
