.roll-up-filter {
  box-sizing: border-box;
  width: 100%;
  margin-left: auto;
  margin-right: auto;
  padding: 0;
  padding-top: 7px;
  padding-bottom: 4px;

  .bold-font;
  font-size: 16.69px;
  line-height: 29.45px;
  color: @secondary-color-font-button;

  word-break: break-all;

  text-transform: uppercase;

  border: none;
  border-radius: 25px;
  background-color: @filter-button-bg;

  cursor: pointer;

  .roll-up-filter__icon {
    width: 12px;
    height: 12px;
    margin-right: 3%;

    fill: @main-dark-color;
  }

  &:hover {
    background-color: @filter-button-bh-hover;
  }

  &:active {
    color: @main-dark-color-opac-30;

    .roll-up-filter__icon {
      fill: @main-dark-color-opac-30;
    }
  }
}

@media (min-width: @tablet-width) {
  .roll-up-filter {
    width: 96%;
    padding-top: 38px;
    padding-bottom: 32px;

    font-size: 20px;
    line-height: 30px;

    .roll-up-filter__icon {
      width: 18px;
      height: 18px;
      margin-right: 2%;
    }
  }
}

@media (min-width: @desktop-width) {
  .roll-up-filter {
    width: 100%;
    padding-top: 37px;

    .roll-up-filter__icon {
      margin-right: 1.2%;
    }
  }
}
