.subscription {
  box-sizing: border-box;
  position: relative;
  z-index: 2;
  margin-top: -30px;

  background-color: @dark-bg;
  border-bottom-left-radius: 30px;
  border-bottom-right-radius: 30px;
}

.subscription__wrapper {
  box-sizing: border-box;
  width: 84%;
  margin-left: auto;
  margin-right: auto;
  padding-top: 102px;
  padding-bottom: 41px;
}

.subscription__title {
  .indents-reset;
  width: 100%;
  margin-bottom: 19px;

  .title-font;
  color: @index-section-title;
  text-align: center;
}

.subscription__paragraph {
  .indents-reset;
  width: 74%;
  margin-bottom: 29px;
  margin-left: auto;
  margin-right: auto;

  color: @clear-font;
  text-align: center;

  word-wrap: break-word;

  &:last-of-type {
    width: 80%;
    margin-bottom: 33px;

    span:last-of-type {
      width: 100%;
    }
  }

  span:first-of-type {
    display: block;
  }
}

.subscription__lets-authentification {
  display: block;
}

.subscription__email-form {
  margin-bottom: 29px;
}

.subscription__autentification-link {
  color: @yellow-button-color;
  text-decoration: underline;

  &:hover {
    color: @index-section-title;
  }

  &:active {
    color: @index-section-title-active;
  }
}

@media (min-width: @tablet-width) {
  .subscription {
    margin-top: -55px;

    border-bottom-left-radius: 60px;
    border-bottom-right-radius: 64px;
  }

  .subscription__wrapper {
    width: 89%;
    padding-top: 132px;
    padding-bottom: 80px;
    padding-left: 4.2%;
  }

  .subscription__title {
    width: 70%;
    margin-left: 12.5%;
    margin-bottom: 57px;

    font-size: 45px;
    line-height: 45px;
  }

  .subscription__lets-authentification {
    display: block;
    margin-right: 6%;
  }

  .subscription__paragraph {
    width: 100%;
    margin-bottom: 66px;

    font-size: 28px;
    line-height: 33px;

    text-align: center;

    &:first-of-type {
      margin-bottom: 63px;
      display: flex;
      flex-wrap: wrap;
      justify-content: center;
    }

    &:last-of-type {
      width: 100%;
      padding-left: 1.2%;

      text-align: left;

      word-break: break-all;

      span:first-of-type {
        margin: 0;
      }
    }

    &:nth-of-type(2) {
      margin-bottom: 62px;
    }

    span:first-of-type {
      display: inline-block;
      margin-right: 1%;

      word-break: break-all;
    }

    span:nth-of-type(3) {
      display: block;

      word-break: break-all;
    }
  }

  .subscription__fast-free-span {
    margin-right: 6%;

    word-break: break-all;
  }

  .subscription__email-form {
    margin-left: 0;
    margin-right: 4.7%;
    margin-bottom: 59px;
  }
}

@media (min-width: @desktop-width) {
  .subscription__wrapper {
    width: 91%;
    max-width: 1462px;
    padding-top: 162px;
    padding-bottom: 96px;
    padding-left: 4.6%;
    padding-right: 4.8%;
  }

  .subscription__title {
    width: 34%;
    margin-left: 33.5%;
    margin-right: auto;
    margin-bottom: 56px;
  }

  .subscription__paragraph {
    margin-bottom: 58px;

    &:first-of-type {
      width: 56%;
      margin: 0;
      margin-left: 23.8%;
      margin-bottom: 58px;
    }

    &:last-of-type {
      width: 60%;
      margin-left: 22.5%;
      margin-bottom: 75px;
    }

    .subscription__answer-span {
      letter-spacing: 1.1px;
    }
  }

  .subscription__email-form {
    margin: 0;
    margin-bottom: 68px;
  }

  .subscription__contacts {
    margin: 0;
    margin-left: auto;
    margin-right: auto;
  }
}
