.continents {
  width: 43%;
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;

  list-style: none;
}

.continents__item {
  &:not(:last-of-type) {
    margin-bottom: 5px;
  }
}

.continents__link {
  .bold-font;
  color: @main-dark-color-opac-30;

  text-transform: uppercase;

  word-break: break-all;

  &:hover {
    color: @main-dark-color-opac-60;
  }

  &:active {
    color: @main-dark-color;
  }
}

@media (min-width: @tablet-width) {
  .continents {
    width: 61%;
    flex-direction: row;
  }

  .continents__item {
    &:not(:last-of-type) {
      margin-right: 7%;
      margin-bottom: 0;
    }
  }

  .continents__link {
    font-size: 20px;
    line-height: 24px;
  }
}

@media (min-width: @desktop-width) {
  .continents {
    position: relative;
    top: 0;
    width: 33%;
  }

  .continents__item {
    &:not(:last-of-type) {
      margin-right: 6%;
    }
  }
}
