.level-range__level-controls {
  position: relative;
  width: 98%;
  height: 34px;
  margin-bottom: 22px;
  display: flex;

  background-color: @clear-bg;
  border: 2px solid @border-color;
  border-radius: 5px;

  &::before {
    content: "";
    position: absolute;
    top: 30%;
    left: 46%;
    z-index: 1;

    width: 16px;
    height: 11px;

    background: @clear-bg;
  }

  &::after {
    content: "";
    position: absolute;
    top: 46%;
    left: 46%;
    z-index: 2;

    width: 16px;
    height: 1px;

    background-color: @border-color;
  }
}

.level-range__level {
  position: relative;
  display: inline-block;
  width: 50%;
  height: 100%;

  border-right: 1px solid @border-color;

  cursor: pointer;

  &::after {
    content: none;
    position: absolute;
    top: 0;
    left: 0;
    display: block;
    width: 100%;
    height: 100%;

    border: 1px solid @level-range-border-hover;
  }

  &:hover::after {
    content: "";
  }

  &:focus::after {
    content: "";
    border-color: @dark-font-nav;
  }

  input {
    width: 50px;
    margin: 0;
    padding-left: 38%;
    padding-top: 12px;

    .medium-font;
    font-size: 15px;
    line-height: 13.5px;
    color: @primary-dark-font-color;

    background: none;
    border: none;
    outline: none;
  }

  &--min {
    input {
      padding-left: 43%;
    }
  }
}

.level-range__range-buttonts {
  position: relative;
}

.level-range__scale {
  width: 218px;
  height: 2px;

  background: @no-active-primary-dark;
}

.level-range__bar {
  width: 148px;
  height: 2px;
  margin-left: auto;

  background: @primary-dark-font-color;
}

.level-range__toggle {
  position: absolute;
  top: -4px;

  width: 10px;
  height: 10px;

  background-color: @primary-dark-font-color;
  border-radius: 50%;
  cursor: pointer;

  &:hover {
    transform: scale(1.46);
  }

  &--min {
    left: 67px;
  }

  &--max {
    left: 210px;
  }
}

@media (min-width: @tablet-width) {
  .level-range {
    width: 400px;
  }

  .level-range__range-buttonts {
    width: 378px;
    margin-top: 1px;
    padding-top: 21px;
    padding-left: 24px;
    flex-grow: 1;
    order: -1;
  }

  .level-range__level-controls {
    width: 157px;
    height: 43px;
    margin-right: 16px;

    &::after {
      top: 45%;
      left: 45%;
    }
  }

  .level-range__level {
    input {
      padding-left: 33%;

      font-size: 20px;
      line-height: 18px;
    }

    &--max {
      input {
        padding-left: 22%;
      }
    }
  }

  .level-range__scale {
    width: 273px;
    height: 3px;
  }

  .level-range__bar {
    width: 73%;
    height: 3px;
  }

  .level-range__toggle {
    position: absolute;
    width: 13px;
    height: 13px;

    &--min {
      left: 92px;
      top: 15px;
    }

    &--max {
      left: 292px;
      top: 15px;
    }
  }
}

@media (min-width: @desktop-width) {
  .level-range {
    width: 202px;
  }

  .level-range__level-controls {
    width: 190px;
  }

  .level-range__level {
    &--min {
      input {
        padding-top: 14px;
        padding-left: 40%;
      }
    }

    &--max {
      input {
        padding-top: 14px;
        padding-left: 31%;
      }
    }
  }

  .level-range__range-buttonts {
    width: 185px;
    padding-left: 2%;
    padding-top: 14px;
  }

  .level-range__scale {
    width: 96%;
  }

  .level-range__bar {
    width: 68%;
  }

  .level-range__toggle {
    &--min {
      left: 32%;
      top: 9px;
    }

    &--max {
      left: 93%;
      top: 9px;
    }
  }
}
