.add-plan {
  background-color: @plan-form-bg;

  border-top-left-radius: 30px;
  border-top-right-radius: 30px;
}

.add-plan__title-wrapper {
  box-sizing: border-box;
  width: 84%;
  margin-left: auto;
  margin-right: auto;
  margin-bottom: 28px;
  padding-top: 30px;
  display: flex;
}

.add-plan__title {
  .indents-reset;
  width: 68%;
  margin-right: 8%;

  .title-font;
  color: @primary-dark-font-color;

  word-break: break-all;
}

.add-plan__step-link {
  margin: 0;
}

.add-plan__steps-wrapper {
  box-sizing: border-box;
  width: 84%;
  margin-left: auto;
  margin-right: auto;
  padding-bottom: 64px;
}

@media (min-width: @tablet-width) {
  .add-plan__title-wrapper {
    position: relative;
    width: 89%;
    margin-bottom: 49px;
    padding-top: 47px;
    padding-left: 4.4%;
    padding-right: 4.4%;
  }

  .add-plan__title {
    margin: 0;
    margin-right: 27%;

    font-size: 36px;
    line-height: 50px;
  }

  .add-plan__steps-wrapper {
    width: 81%;
    padding-bottom: 84px;
  }
}

@media (min-width: @desktop-width) {
  .add-plan {
    border-top-left-radius: 60px;
    border-top-right-radius: 60px;
  }

  .add-plan__title-wrapper {
    position: relative;
    width: 91%;
    max-width: 1462px;
    margin-bottom: 49px;
    padding-top: 49px;
    padding-left: 4.5%;
  }

  .add-plan__steps-wrapper {
    width: 91.6%;
    max-width: 1462px;
    padding-bottom: 164px;
  }
}
