.filter-title {
  box-sizing: border-box;
  position: relative;
  width: 85%;
  padding: 0;
  padding-top: 8px;

  .bold-font;
  font-size: 16px;
  color: @main-dark-color;


  text-transform: uppercase;

  word-break: break-all;

  svg {
    margin-right: 3%;
  }

  & span {
    display: none;
  }
}

@media (min-width: @tablet-width) {
  .filter-title {
    display: none;
  }
}

@media (min-width: @desktop-width) {
  .filter-title {
    display: block;
    width: 24.6%;
    padding-top: 0;

    font-size: 20px;
    line-height: 24px;

    & span {
      display: inline;
    }

    svg {
      width: 23px;
      height: 15px;
      margin-right: 5%;
    }
  }
}
