.pagination-buttons {
  box-sizing: border-box;
  width: 270px;
  margin-left: auto;
  margin-right: auto;
  display: flex;
}

.pagination-buttons__item {
  box-sizing: border-box;
  width: 136px;
  padding: 0;
  padding-top: 11px;
  padding-bottom: 15px;

  background-color: @clear-bg;
  border: none;

  cursor: pointer;

  .pagination-buttons__item-icon {
    position: relative;
    top: 3px;
    width: 10px;
    height: 20px;
    fill: @main-dark-color;

    &--back-button {
      transform: rotate(180deg);
    }
  }

  &:hover {
    .pagination-buttons__item-icon {
      fill: @main-dark-color-opac-60;
    }
  }

  &:active {
    .pagination-buttons__item-icon {
      fill: @main-dark-color-opac-30;
    }
  }

  &--back-button {
    border-top-left-radius: 20px;
    border-bottom-left-radius: 20px;
    border-right: 1px solid @border-color;
  }

  &--next-button {
    border-top-right-radius: 20px;
    border-bottom-right-radius: 20px;
  }
}

.pagination-buttons__pages-list {
  .list-res;
  display: none;
}

.pagination-buttons__page {
  display: inline-block;
  width: 50px;
  height: 50px;
  text-align: center;

  cursor: pointer;

  &:hover {
    .pagination-buttons__link {
      color: @pagination-hover;
    }
  }

  &:active {
    background-color: @yellow-button-color;
    border-radius: 50%;

    .pagination-buttons__link {
      color: @dark-font-nav;
    }
  }
}

@media (min-width: @tablet-width) {
  .pagination-buttons {
    width: 688px;
    padding-top: 26px;
    padding-bottom: 23px;

    background-color: @clear-bg;
    border-radius: 20px;
  }

  .pagination-buttons__pages-list {
    margin-right: auto;
    margin-left: 4%;
    display: flex;
  }

  .pagination-buttons__item {
    width: 62px;

    svg {
      position: relative;
    }

    &--back-button {
      border-right: none;

      svg {
        left: 9px;
      }
    }

    &--next-button {
      border-right: none;

      svg {
        left: -9px;
      }
    }
  }

  .pagination-buttons__page {
    align-self: center;
  }

  .pagination-buttons__link {
    display: block;
    height: 100%;
    padding-top: 11px;

    .bold-font;
    font-size: 20px;
    line-height: 30px;
    color: @dark-font-nav;
  }
}

@media (min-width: @desktop-width) {
  .pagination-buttons {
    width: 950px;
    margin-left: 0;
  }

  .pagination-buttons__pages-list {
    margin-left: 3%;
  }

  .pagination-buttons__item {
    &--back-button {
      svg {
        left: 0;
        top: 2px;
      }
    }

    &--next-button {
      svg {
        left: -18px;
        top: 2px;
      }
    }
  }
}
