.social-links {
  padding: 0;
  width: 135px;
  margin-left: auto;
  margin-right: auto;
  display: flex;
  flex-wrap: wrap;

  list-style: none;

  &--menu {
    width: 50%;
    margin-bottom: 0;
  }
}

.social-links__item {
  box-sizing: border-box;
  width: 45px;
  height: 45px;

  border-radius: 50%;

  &--yellow-back {
    background-color: @yellow-button-color;
  }

  &--footer {
    svg {
      fill: @font-yellow-button;
    }

    &:hover {
      fill: inherit;
      background-color: @yellow-button-hover-bg;
    }

    &:active {
      color: @font-yellow-button-active;

      .social-links__link svg {
        fill: @font-yellow-button-active;
      }
    }
  }
}

.social-links__link {
  display: block;
  height: 100%;
  padding-left: 46%;

  &--footer-fb {
    padding-left: 40.5%;
    padding-top: 13px;
  }

  &--footer-vk {
    padding-left: 25.5%;
    padding-top: 15px;
  }

  &--footer-inst {
    padding-left: 31.5%;
    padding-top: 15px;
  }

  &--vk-menu {
    padding-left: 34%;
  }

  .social-links__icon {
    fill: @main-dark-color;

    &:hover {
      fill: @main-dark-color-opac-60;
    }

    &:active {
      fill: @main-dark-color-opac-30;
    }
  }
}

.social-links__fb-icon {
  width: 11px;
  height: 21px;
}

.social-links__vk-icon {
  width: 23px;
  height: 12px;
}

.social-links__inst-icon {
  width: 18px;
  height: 18px;
}

@media (min-width: @tablet-width) {
  .social-links {
    &--footer {
      width: 27%;
    }

    &--menu {
      margin-left: 11.2%;

      .social-links__item {
        margin-right: 3%;

        &:nth-of-type(2) {
          margin-right: 4.5%;
        }
      }
    }
  }

  .social-links__item {
    &--footer {
      width: 60px;
      height: 60px;
    }
  }

  .social-links__fb-icon {
    width: 14px;
    height: 28px;
  }

  .social-links__vk-icon {
    width: 31px;
    height: 16px;
  }

  .social-links__inst-icon {
    width: 24px;
    height: 24px;
  }

  .social-links__link {
    &--vk-menu {
      padding-top: 7px;
    }

    &--inst-menu {
      padding-top: 2px;
    }

    &--footer-fb {
      padding-top: 16px;
      padding-left: 21px;
    }

    &--footer-vk {
      padding-top: 23px;
      padding-left: 12px;
    }

    &--footer-inst {
      padding-top: 18px;
      padding-left: 17px;
    }
  }
}

@media (min-width: @desktop-width) {
  .social-links {
    &--menu {
      display: none;
    }

    &--footer {
      width: 13.8%;
    }
  }

  .social-links__link {
    &--footer-fb {
      padding-top: 15px;
      padding-left: 23px;
    }

    &--footer-vk {
      padding-top: 23px;
      padding-left: 15px;
    }

    &--footer-inst {
      padding-top: 18px;
      padding-left: 20px;
    }
  }
}
