.about {
  box-sizing: border-box;
  position: relative;
  padding-bottom: 81px;

  background-color: @header-bg;
  border-bottom-left-radius: 27px;
  border-bottom-right-radius: 27px;
  background-image: url("../img/about-mobile@1x.png"), url("../img/blue-back.svg");
  background-repeat: no-repeat;
  background-size: 292px 352px, 132px 132px;
  background-position: 17px 39px, 0 39px;

  @media (min-resolution: @retina-dpi), (min-resolution: @retina-dppx) {
    background-image: url("../img/about-mobile@2x.png"), url("../img/blue-back.svg");
  }
}

.about__wrapper {
  width: 84%;
  padding-top: 72px;
  margin-left: auto;
  margin-right: auto;
}

.about__title {
  .indents-reset;

  .title-font;
  margin-left: 8.5%;
  margin-bottom: 19px;
  color: @index-section-title;
}

.about__paragraph {
  .indents-reset;
  width: 83%;
  margin-left: 8.5%;
  margin-bottom: 19px;

  color: @clear-font;

  br:last-of-type {
    display: none;
  }
}

.about__condition {
  .indents-reset;
  width: 73%;
  margin-left: 8.5%;

  .bold-font;
  color: @clear-font;
}

.about__show-more {
  box-sizing: border-box;
  display: none;
  width: 274px;
  padding-right: 2px;

  .bold-font;
  color: @font-yellow-button;

  word-wrap: break-word;

  background-color: @yellow-button-color;
}

@media (min-width: @tablet-width) {
  .about {
    padding-bottom: 88px;

    border-bottom-left-radius: 58px;
    border-bottom-right-radius: 58px;

    background-color: @header-bg;
    background-image: url("../img/about-tablet@1x.png"), url("../img/blue-back_tablet.svg");
    background-repeat: no-repeat;
    background-size: 376px 548px, 688px 213px;
    background-position: 98.26% 144px, 40px 96px;

    @media (min-resolution: @retina-dpi), (min-resolution: @retina-dppx) {
      background-image: url("../img/about-tablet@2x.png"), url("../img/blue-back_tablet.svg");
      background-repeat: no-repeat;
      background-size: 376px 548px, 688px 213px;
      background-position: 98.26% 142px, 40px 96px;
    }
  }

  .about__wrapper {
    padding-top: 138px;
    width: 89%;
  }

  .about__title {
    margin-left: 4.7%;
    margin-bottom: 40px;

    font-size: 45px;
    line-height: 45px;
  }

  .about__paragraph {
    width: 44%;
    margin-left: 4.9%;
    margin-bottom: 32px;

    font-size: 28px;
    line-height: 33px;

    br:first-of-type {
      display: none;
    }

    br:last-of-type {
      display: block;
    }
  }

  .about__condition {
    width: 38%;
    margin-left: 4.8%;
    margin-bottom: 51px;

    font-size: 21px;
    line-height: 25px;
  }

  .about__show-more {
    box-sizing: border-box;
    position: relative;
    display: block;
    width: 40%;
    margin-left: 4.8%;
    padding-top: 30px;
    padding-bottom: 26px;
    padding-left: 4.7%;

    font-size: 24px;
    line-height: 24px;

    border-radius: 20px;

    .button-actions;
  }

  .about__show-more-icon {
    margin-left: 30%;
    fill: @font-yellow-button;
  }
}

@media (min-width: @desktop-width) {
  .about {
    margin-top: -60px;
    padding-bottom: 164px;

    background: url("../img/about-desktop@1x.png"), url("../img/blue-back_desktop.png");
    background-color: @header-bg;
    background-repeat: no-repeat;
    background-size: 630px 665px, 213px 213px;
    background-position: 92.8% 145px, 60px 132px;

    @media (min-resolution: @retina-dppx), (min-resolution: @retina-dpi) {
      background: url("../img/about-desktop@2x.png"), url("../img/blue-back_desktop.png");
      background-color: @header-bg;
      background-repeat: no-repeat;
      background-size: 630px 665px, 213px 213px;
      background-position: 92.8% 145px, 60px 132px;
    }
  }

  .about__wrapper {
    width: 92%;
    max-width: @max-width-wrapper;
    padding-top: 193px;
  }

  .about__title {
    margin: 0;
    padding-left: 5.4%;
    margin-bottom: 57px;
  }

  .about__paragraph {
    width: 47%;
    margin-left: 5.5%;
    margin-bottom: 39px;

    br {
      display: none;

      &:last-of-type {
        display: none;
      }
    }
  }

  .about__condition {
    width: 31%;
    margin: 0;
    margin-bottom: 84px;
    padding-left: 5.4%;
  }

  .about__show-more {
    width: 21%;
    padding-left: 2.6%;
    margin-left: 5.4%;
  }
}
