.business-tariffs {
  box-sizing: border-box;
  position: absolute;
  top: -60px;
  width: 84.3%;

  background-color: @clear-bg;
  border-radius: 20px;

  box-shadow: 0 20px 50px 0 @shadow-popup;
}

.business-tariffs__wrapper {
  box-sizing: border-box;
  padding-top: 10px;
}

.business-tariffs__title {
  color: @index-section-title;
  text-align: center;
}

.business-tariffs__rates {
  margin-bottom: 33px;
}

.business-tariffs__close-window {
  display: block;
  padding-top: 20px;
  padding-bottom: 20px;

  .bold-font;
  line-height: 24px;
  color: @primary-dark-font-color;

  text-transform: uppercase;
  text-align: center;

  background-color: @close-window-bg;

  border-bottom-left-radius: 20px;
  border-bottom-right-radius: 20px;

  cursor: pointer;

  &:hover {
    background-color: @light-bg;
  }

  &:active {
    color: @primary-dark-font-active;
  }
}

@media (min-width: @tablet-width) {
  .business-tariffs {
    width: 89.5%;
    right: 5.5%;

    border-radius: 60px;
  }

  .business-tariffs__title {
    font-size: 45px;
    line-height: 45px;
  }

  .business-tariffs__close-window {
    padding-top: 40px;
    padding-bottom: 40px;

    font-size: 24px;

    text-transform: none;

    border-bottom-left-radius: 60px;
    border-bottom-right-radius: 60px;
  }
}

@media (min-width: @desktop-width) {
  .business-tariffs {
    top: 33px;
    right: 24%;
    width: 752px;
  }
}
