.transportation-types {
  padding: 0;
  display: flex;
  flex-wrap: wrap;

  list-style: none;

  &--index {
    width: 124px;
  }

  &--check {
    margin: 0;
    flex-direction: row;
    justify-content: flex-start;
  }

  &--catalog {
    margin: 0;
    margin-left: 15%;
    padding-top: 4px;
    justify-content: flex-start;
  }

  &--profile-info {
    width: 47%;
    height: 32px;
    margin: 0;
    margin-left: auto;
    margin-right: auto;
  }
}

.transportation-types__item {
  box-sizing: border-box;
  display: block;

  background-color: @clear-bg;

  cursor: pointer;

  &--air,
  &--vehicle,
  &--bicycle,
  &--foot {
    border-radius: 50%;
  }

  .transportation-types__item-icon {
    fill: @no-active-basic-font;
  }

  &--travelmates {
    box-sizing: border-box;
    position: relative;
    width: 32px;
    height: 33px;

    background-color: @light-bg;

    .transportation-types__travelmates-icon {
      position: absolute;
      left: 7px;
      top: 8px;
      fill: @font-color-value-15proc-down;
    }
  }

  &--travelmates&--active {
    .transportation-types__travelmates-icon {
      fill: @primary-dark-font-color;
    }
  }

  &--check-box {
    box-sizing: border-box;
    position: relative;
    width: 32.25px;
    height: 32.25px;

    cursor: pointer;

    .transportation-types__check-icon {
      position: absolute;
      left: 6px;
      top: 8px;
    }

    &:active {
      background-color: @activ-icon-check-bg;
    }

    &:disabled:active {
      background-color: @activ-icon-check-bg;
    }
  }

  &--check-box-form {
    &:first-of-type {
      margin-left: -44%;
    }
  }

  &--user-cart {
    position: relative;

    &:not(:last-of-type) {
      margin-right: 14px;
    }

    &::after {
      content: attr(data-hover);
      position: absolute;
      top: 25px;
      left: -150%;
      z-index: 1;
      width: auto;
      padding-top: 18px;
      padding-bottom: 10px;
      padding-left: 10px;
      padding-right: 10px;

      .regular-font;
      font-size: 20px;
      line-height: 24px;
      color: @clear-bg;

      text-align: center;

      background-color: @hover-tooltype-bg;

      border-radius: 5px;

      opacity: 0;
      visibility: hidden;
    }

    &::before {
      content: "";
      position: absolute;
      top: 20px;
      width: 0;
      height: 0;
      border-left: 9px solid transparent;
      border-right: 9px solid transparent;
      border-bottom: 10px solid @hover-tooltype-bg;

      opacity: 0;
      visibility: hidden;
    }

    &:hover::after {
      opacity: 1;
      visibility: visible;
    }

    &:hover::before {
      opacity: 1;
      visibility: visible;
    }
  }
}

.transportation-types__item--active .transportation-types__item-icon {
  fill: @primary-dark-font-color;
}

.transportation-types__check {
  .visually-hidden;

  & ~ .transportation-types__check-icon {
    fill: @font-color-value-15proc-down;
  }

  &:disabled ~ .transportation-types__check-icon {
    fill: @font-color-value-20proc-down;
  }

  &:checked ~ .transportation-types__check-icon {
    fill: @primary-dark-font-color;
  }

  &:focus ~ .transportation-types__check-icon {
    outline: rgb(59, 153, 252) auto 5px;
  }

  &:hover ~ .transportation-types__check-icon {
    fill: @font-color-value;
  }

  &:disabled:active ~ .transportation-types__check-icon {
    fill: @font-color-value-20proc-down;
  }

  &:checked:hover ~ .transportation-types__check-icon {
    fill: @primary-dark-font-color-opac-70;
  }

  &:checked:active ~ .transportation-types__check-icon {
    fill: @checkbox-icon-active;
  }
}

@media (min-width: @tablet-width) {
  .transportation-types {
    width: 100%;

    &--index {
      width: 125px;

      .transportation-types__item {
        &:nth-of-type(3) {
          margin-right: 11px;
        }
      }
    }

    &--catalog {
      margin: 0;
      padding: 0;
      padding-left: 20px;
    }
  }

  .transportation-types__item {
    &--user-cart {
      &::after {
        top: 25px;
      }
    }

    &--check-box {
      width: 43px;
      height: 43px;

      .transportation-types__check-icon {
        left: 11px;
        top: 11px;
        width: 19px;
        height: 21px;
      }
    }

    &--travelmates {
      margin: 0;

      background-color: @clear-bg;

      .transportation-types__travelmates-icon {
        left: 0;
        top: -1px;
      }

      .transportation-types__travelmates-icon.transportation-types__travelmates-icon--foot-demin {
        fill: @font-color-value-15proc-down;
      }
    }

    &--air {
      .transportation-types__travelmates-icon {
        width: 19px;
        height: 21px;
      }
    }

    &--vehicle {
      .transportation-types__travelmates-icon {
        width: 17px;
        height: 21px;
      }
    }

    &--bicycle {
      .transportation-types__travelmates-icon {
        width: 24px;
        height: 21px;

        &--bicycle-demin {
          fill: @primary-dark-font-color;
        }
      }
    }

    &--foot {
      .transportation-types__travelmates-icon {
        width: 20px;
        height: 21px;
      }
    }
  }
}

@media (min-width: @desktop-width) {
  .transportation-types {
    &--catalog {
      margin: 0;
      flex-direction: row;
    }
  }

  .transportation-types__item {
    &--travelmates {
      .transportation-types__travelmates-icon {
        left: 7px;
        top: 10px;
      }
    }
  }

  .transportation-types__item--user-cart::after {
    top: 24px;
  }
}
