.site-nav {
  width: 72%;
  padding: 0;

  list-style: none;
}

.site-nav__link {
  width: 100%;
  display: block;

  .medium-font;
  font-size: 30px;
  line-height: 34px;
  color: @main-dark-color;

  text-align: center;
  text-transform: uppercase;

  word-break: break-all;
}

@media (min-width: @tablet-width) {
  .site-nav__link {
    font-size: 60px;
    line-height: 63px;
    text-align: left;
  }

  .site-nav__item {
    &:not(:last-of-type) {
      margin-bottom: 10px;
    }
  }
}

@media (min-width: @desktop-width) {
  .site-nav {
    width: 50%;
    margin: 0;
    padding-top: 13px;
    display: flex;
    flex-wrap: wrap;

    background-color: transparent;
  }

  .site-nav__item {
    align-self: flex-start;

    &:not(:last-of-type) {
      margin-bottom: 0;
      margin-right: 33px;
    }
  }

  .site-nav__link {
    display: inline-block;
    overflow: hidden;

    font-size: 20px;
    line-height: 30px;
    color: @clear-font;

    &--scroll-menu {
      color: @main-dark-color;
    }

    span {
      display: inline-block;
      position: relative;

      transition: 0.3s;
    }

    span::before {
      display: inline-block;
      position: absolute;
      top: 100%;
      content: attr(data-hover);

      .medium-font;
      color: @yellow-button-color;

      transform: translate3d(0, 0, 0);
    }

    &:hover span {
      transform: translateY(-100%);
    }

    &:active {
      opacity: 0.3;
    }
  }
}
