.days-value {
  display: flex;
  flex-wrap: wrap;
}

.days-value__paragraphs-wrapper {
  width: 100%;
  padding-bottom: 8px;
  display: flex;
  justify-content: space-between;
}

.days-value__requirement {
  margin: 0;
  margin-right: 28.5%;
  padding-bottom: 9px;

  .input-field-value-title;
  letter-spacing: 1.4px;
  color: @primary-dark-font-color;
}

.days-value__value {
  .input-field-value-title;
  margin: 0;

  color: @font-color-value;
}

@media (min-width: @tablet-width) {
  .days-value {
    border-top: 1px solid @form-border-second-value;
  }

  .days-value__paragraphs-wrapper {
    padding-top: 58px;
    padding-bottom: 24px;
    order: -1;
  }

  .days-value__requirement {
    margin-right: 63.5%;
    padding-bottom: 23px;
    order: -2;

    font-size: 20px;
    line-height: 18px;
  }

  .days-value__value {
    order: -1;

    font-size: 20px;
    line-height: 18px;
  }
}

@media (min-width: @desktop-width) {
  .days-value {
    width: 50%;
    padding-top: 16px;
    display: flex;

    border: none;
  }

  .days-value__requirement {
    margin: 0;
    margin-right: 10.5%;
    padding: 0;
    align-self: center;
    order: -2;

    letter-spacing: 2.1px;
  }

  .days-value__value {
    align-self: center;
    order: 0;
  }

  .days-value__input {
    margin-right: 7%;
    order: -1;
  }
}
