.copyright {
  box-sizing: border-box;
  padding-top: 20px;
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;

  .regular-font;
  color: inherit;

  border-top: 1px solid @footer-border;

  word-wrap: break-word;

  svg {
    margin-top: -5px;
  }

  &:hover {
    color: @font-color-value-30proc-up;

    svg {
      fill: @font-color-value-30proc-up;
    }
  }

  &:active {
    color: @font-color-value;

    svg {
      fill: @font-color-value;
    }
  }
}

.copyright__description {
  .indents-reset;

  .regular-font;
}

.copyright__link {
  margin-top: -5px;
}

@media (min-width: @tablet-width) {
  .copyright {
    width: 22.5%;
    padding-top: 17px;
    display: flex;
    justify-content: space-between;

    font-size: 20px;
    line-height: 24px;

    border-top: none;

    svg {
      margin-top: -3px;
    }
  }

  .copyright__description {
    font-size: 20px;
    line-height: 24px;
  }
}

@media (min-width: @desktop-width) {
  .copyright {
    width: 12.5%;
    padding-left: 3px;

    svg {
      position: relative;
      left: -10px;
    }
  }
}
