.map {
  position: relative;
  width: 100%;
  height: 546px;
  margin-top: -30px;

  background: url("../img/map-mobile@1x.jpg");
  background-color: @clear-bg;
  background-size: 100% 512px;
  background-repeat: no-repeat;
  background-position: 0 5px;

  @media (min-resolution: @retina-dppx), (min-resolution: @retina-dpi) {
    background: url("../img/map-mobile@2x.jpg");
    background-color: @clear-bg;
    background-size: 320px 512px;
    background-repeat: no-repeat;
    background-position: 0 5px;
  }

  &::before {
    content: "";
    position: absolute;
    z-index: 0;
    top: 123px;
    left: 46%;
    display: block;
    width: 41px;
    height: 41px;

    background-color: @clear-bg;
    background-image: url("../img/map-marker.svg");

    border-radius: 50%;
  }

  iframe {
    position: relative;
    z-index: 1;

    border: none;
  }
}

@media (min-width: @tablet-width) {
  .map {
    height: 700px;
    margin-top: -89px;

    background: url("../img/map-tablet@1x.jpg");
    background-color: @clear-bg;
    background-size: 100% 700px;
    background-repeat: no-repeat;
    background-position: 0 5px;

    @media (min-resolution: @retina-dppx), (min-resolution: @retina-dpi) {
      background: url("../img/map-tablet@2x.jpg");
      background-color: @clear-bg;
      background-size: 100% 700px;
      background-repeat: no-repeat;
      background-position: 0 5px;
    }

    &::before {
      top: 276px;
      left: 46.5%;
      width: 54px;
      height: 54px;
    }

    iframe {
      position: relative;
      z-index: 1;
      height: 700px;
    }
  }
}

@media (min-width: @desktop-width) {
  .map {
    height: 602px;
    margin-top: -89px;

    background: url("../img/map-desktop@1x.jpg");
    background-color: @clear-bg;
    background-size: 100% 750px;
    background-repeat: no-repeat;
    background-position: 0 -110px;

    @media (min-resolution: @retina-dppx), (min-resolution: @retina-dpi) {
      background: url("../img/map-desktop@2x.jpg");
      background-color: @clear-bg;
      background-size: 100% 750px;
      background-repeat: no-repeat;
      background-position: 0 -110px;
    }

    &::before {
      top: 236px;
      left: 48%;
      width: 54px;
      height: 54px;
    }

    iframe {
      position: relative;
      z-index: 1;
      height: 635px;
    }
  }
}
