.contacts {
  padding: 0;

  list-style: none;

  &--subscription {
    .list-res;
    padding-left: 22%;
  }

  &--menu {
    width: 100%;
    padding: 0;
    padding-left: 24%;

    list-style: none;
  }
}

.contacts__icon {
  fill: @yellow-button-color;
}

.contacts__item {
  &:not(:last-of-type) {
    margin-bottom: 23px;
  }

  &--subscription {
    &:not(:last-of-type) {
      margin-bottom: 27px;
    }
  }
}

.contacts__link {
  box-sizing: border-box;
  position: relative;

  .bold-font;
  line-height: 18px;
  color: @main-dark-color;

  text-transform: uppercase;
  text-decoration: none;

  span {
    width: 180px;
    word-wrap: break-word;
  }

  &--yellow {
    color: @yellow-button-color;
  }

  &--subscription {
    width: 100%;
    display: flex;

    span {
      align-self: center;
    }
  }

  &:hover {
    color: @yellow-button-hover-bg;

    .contacts__icon {
      fill: @yellow-button-hover-bg;
    }
  }

  &:active {
    color: @active-yellow-font;

    .contacts__icon {
      fill: @active-yellow-font;
    }
  }
}

.contacts__icon-wrapper {
  box-sizing: border-box;
  position: absolute;
  top: -16px;
  left: -60px;
  display: inline-block;
  width: 45px;
  height: 45px;
  margin-right: 4%;
  padding-top: 8px;
  padding-left: 13px;

  border-radius: 50%;
  background-color: @dark-icon-bg;

  .contacts__icon {
    position: absolute;
    top: 14px;
  }

  .contacts__icon-phone {
    width: 18px;
    height: 18px;
  }

  .contacts__icon-mail {
    width: 18px;
    height: 20px;
  }
}

@media (min-width: @tablet-width) {
  .contacts {
    &--menu {
      padding-left: 23%;

      .contacts__item {
        &:not(:last-of-type) {
          margin-bottom: 37px;
        }
      }
    }

    &--subscription {
      padding-left: 14%;
    }
  }

  .contacts__item {
    &--subscription {
      &:not(:last-of-type) {
        margin-bottom: 37px;
      }
    }
  }

  .contacts__link {
    font-size: 28px;
    line-height: 28px;

    &--subscription {
      font-size: 28px;
      line-height: 28px;

      span {
        width: 315px;
      }
    }
  }

  .contacts__icon-wrapper {
    top: -24px;
    left: -90px;
    width: 68px;
    height: 68px;
    margin-right: 3.8%;
    padding-top: 21px;
    padding-left: 3.3%;

    &--nav-mail {
      margin-right: 2%;
    }

    .contacts__icon {
      top: 20px;
      left: 19px;
    }

    .contacts__icon-phone {
      width: 27px;
      height: 27px;
    }

    .contacts__icon-mail {
      width: 27px;
      height: 32px;
    }

    &--tel {
      margin-right: 3.5%;
    }
  }
}

@media (min-width: @desktop-width) {
  .contacts {
    display: none;

    &--subscription {
      width: 74%;
      margin-left: auto;
      margin-right: auto;
      padding-left: 21%;
      display: flex;
      justify-content: space-between;
      flex-wrap: wrap;
    }
  }

  .contacts__item {
    &--subscription {
      width: 50%;
    }
  }

  .contacts__link {
    &--subscription {
      display: flex;

      font-size: 28px;
      line-height: 28px;

      span {
        align-self: center;
      }
    }
  }

  .contacts__icon-wrapper {
    width: 90px;
    height: 90px;
    margin-right: 7.1%;

    &--tel {
      padding-left: 6%;
      padding-top: 29px;
    }

    &--mail {
      padding-left: 6.1%;
      padding-top: 30px;
      margin-right: 8%;
    }
  }

  .contacts__icon-wrapper .contacts__icon-phone {
    width: 36px;
    height: 36px;
  }

  .contacts__icon-wrapper .contacts__icon-mail {
    width: 36px;
    height: 36px;
  }
}

@media (min-width: @desktop-width) {
  .contacts__icon-wrapper {
    top: -33px;
    left: -125px;

    .contacts__icon {
      top: 28px;
      left: 28px;
    }
  }
}
