.my-plans {
  background-color: @third-background;

  border-top-left-radius: 30px;
  border-top-right-radius: 30px;
}

.my-plans__wrapper {
  box-sizing: border-box;
  width: 84%;
  margin-left: auto;
  margin-right: auto;
  padding-top: 29px;
  padding-bottom: 50px;
}

.my-plans__title {
  .indents-reset;
  margin-bottom: 28px;

  .title-font;
  color: @primary-dark-font-color;

  word-break: break-all;
}

@media (min-width: @tablet-width) {
  .my-plans__wrapper {
    width: 89%;
    padding-top: 47px;
    padding-bottom: 97px;
    padding-left: 4%;
    padding-right: 4%;
  }

  .my-plans__title {
    margin-bottom: 42px;

    font-size: 36px;
    line-height: 50px;
  }
}

@media (min-width: @desktop-width) {
  .my-plans {
    border-top-left-radius: 60px;
    border-top-right-radius: 60px;
  }

  .my-plans__wrapper {
    width: 91.6%;
    max-width: 1462px;
    padding-top: 48px;
    padding-bottom: 122px;
    padding-left: 4.5%;
    padding-right: 0;
  }

  .my-plans__title {
    padding-left: 5px;
  }
}
