.rates {
  position: relative;
  width: 100%;
  padding: 0;

  border-spacing: 0;

  &--business-tariffs {
    width: 88%;
    padding-left: 25px;
    padding-right: 26px;
  }

  &::before {
    content: "";
    position: absolute;
    right: 0;
    top: -130px;
    display: block;
    width: 99px;
    height: 99px;

    background: url("../img/rates@1x.png");
    background-repeat: no-repeat;
    background-size: contain;

    @media (min-resolution: @retina-dppx), (min-resolution: @retina-dpi) {
      background: url("../img/rates@2x.png");
      background-repeat: no-repeat;
      background-size: contain;
    }
  }

  &--business-tariffs::before {
    content: none;
  }
}

.rates__item {
  box-sizing: border-box;
  position: relative;
  width: 80%;
  padding-top: 11px;
  padding-bottom: 10px;
  display: flex;
  flex-wrap: wrap;

  align-self: flex-start;

  border-top: @rates-border;

  &:last-of-type {
    border-bottom: @rates-border;
  }
}

.rates__description-list {
  width: 95%;
  margin: 0;
}

.rates__description-term {
  width: 100%;
  margin-right: auto;
  flex-grow: 1;

  word-wrap: break-word;

  &::before {
    box-sizing: border-box;
    position: absolute;
    top: 3px;
    right: -25%;
    width: 34px;
    height: 34px;
    padding-left: 12px;
    padding-top: 12px;

    .regular-font;
    font-size: 16px;
    line-height: 14px;
    color: @clear-font;

    border-radius: 50%;
  }

  &--one::before {
    content: "1";
    background-color: @features-color-two;
  }

  &--two::before {
    content: "2";
    background-color: @features-color-three;
  }

  &--three::before {
    content: "3";
    background-color: @features-color-four;
  }
}

.rates__description-price {
  width: 50%;
  margin: 0;
  margin-right: 0;
  word-wrap: break-word;
}

@media (min-width: @tablet-width) {
  .rates {
    width: 87%;
    margin-left: 13%;

    &::before {
      top: 108%;
      right: 43px;
    }

    &::after {
      content: "";
      position: absolute;
      z-index: -2;
      top: -43px;
      right: -2px;
      display: block;
      width: 198px;
      height: calc(100% + 86px);

      background-color: @bg-country-filter;
      border-radius: 18px;

      opacity: 0.7;
    }

    &--business-tariffs {
      width: 85%;
      margin: 0;
      padding: 0;
      margin-left: auto;
      margin-right: auto;

      .rates__item {
        width: 100%;
      }
    }
  }

  .rates__item {
    width: 95%;
    padding-top: 17px;
    padding-bottom: 16px;
  }

  .rates__description-list {
    width: 100%;
    display: flex;
  }

  .rates__description-term {
    width: 58%;

    font-size: 28px;
    line-height: 33px;

    &--business {
      font-size: 22px;
      line-height: 28px;
    }

    &::before {
      box-sizing: border-box;
      position: absolute;
      top: -2px;
      left: -16%;
      width: 67px;
      height: 67px;
      padding-left: 24px;
      padding-top: 21px;

      font-size: 28px;
      line-height: 28px;
    }
  }

  .rates__description-price {
    width: 29%;
    padding-bottom: 0;
    padding-right: 0.5%;

    font-size: 28px;
    line-height: 33px;

    text-align: right;

    &--business {
      width: 35%;
      font-size: 22px;
      line-height: 28px;
    }
  }

  .rates__number {
    position: relative;

    margin-right: 2%;
    padding-top: 20px;
    padding-left: 20px;
    order: 0;

    font-size: 28px;
    line-height: 28px;
  }
}

@media (min-width: @desktop-width) {
  .rates {
    width: 74%;
    margin: 0;
    margin-left: 12%;
    margin-right: auto;

    &::before {
      right: -12%;
      top: 2px;
      width: 198px;
      height: 198px;
    }

    &::after {
      top: -50px;
      width: 444px;
      height: 150%;
    }
  }

  .rates__item {
    width: 84%;
  }

  .rates__description-term::before {
    left: -11.5%;
    padding-left: 28px;
    padding-top: 23px;
  }
}
