.select-letter {
  width: 100%;
  display: none;

  box-shadow: 0 20px 50px 0 @shadow-popup;
  background-color: @clear-bg;
}

.select-letter__countries-list {
  box-sizing: border-box;
  width: 99%;
  padding-top: 13px;
  padding-bottom: 14px;
  padding-left: 9%;

  border-left: 1px solid @border-color;
  border-bottom: 1px solid @border-color;
  border-right: 1px solid @border-color;

  border-bottom-left-radius: 10px;
  border-bottom-right-radius: 10px;
}

@media (min-width: @tablet-width) {
  .select-letter {
    width: calc(100% + 57%);
    display: flex;
  }

  .select-letter__countries-list {
    box-sizing: border-box;
    width: 38%;
    padding-top: 35px;
    padding-left: 4.3%;

    border: none;
    border-left: 1px solid @countries-list-border;
    border-bottom-left-radius: 0;
  }
}

@media (min-width: @desktop-width) {
  .select-letter {
    width: 733px;
    border-radius: 20px;
  }

  .select-letter__countries-list {
    padding-top: 32px;
    padding-left: 25px;
    padding-bottom: 80px;

    &--one-column-form {
      display: flex;
    }
  }
}
