.main-nav {
  box-sizing: border-box;
  width: 100%;

  &::after {
    content: "";
    position: absolute;
    top: 478px;
    display: block;
    width: 100%;
    height: 285px;

    background-color: @dark-font-nav;
  }

  &--nojs {
    width: 100%;
    position: relative;

    &::after {
      content: none;
    }

    .main-nav__logo {
      .main-nav__logo-image--white {
        display: none;
      }
    }

    .main-nav__toggle {
      display: none;
    }

    .main-nav__list--site-nav,
    .main-nav__list--login,
    .main-nav__list--menu-contacts {
      display: block;
    }

    .main-nav__list--social {
      display: flex;
    }

    background-color: @clear-bg;
  }

  &--form,
  &--catalog {
    top: 0;

    &::after {
      content: none;
    }
  }

  @media (max-width: @max-width-nav-menu) {
    &--closed {
      position: relative;

      background-color: transparent;

      .main-nav__list--site-nav,
      .main-nav__list--login,
      .main-nav__list--menu-contacts,
      .main-nav__list--social {
        display: none;
      }
    }

    &--opened {
      &::after {
        content: none;
      }

      position: absolute;
      z-index: 4;

      background-color: @clear-bg;
    }

    &--opened.main-nav--form {
      z-index: 4;
    }
  }

  &--scroll-menu {
    position: relative;
    z-index: 2;
    background-color: @clear-bg;

    .main-nav__logo {
      margin-bottom: 0;

      .main-nav__logo-image--blue {
        display: block;
      }

      .main-nav__logo-image--white {
        display: none;
      }
    }
  }

  &--closed&--scroll-menu {
    .main-nav__toggle {
      display: block;

      &::before {
        background-color: @nav-bth-color;
      }

      &::after {
        background-color: @nav-bth-color;
      }
    }
  }
}


.main-nav__wrapper {
  box-sizing: border-box;
  padding-top: 27px;
  padding-bottom: 15px;
  margin-left: 6%;
  margin-right: 8%;
  display: flex;
  flex-wrap: wrap;

  &--catalog {
    padding-top: 18px;
  }

  &--form {
    padding-top: 19px;
  }
}

.main-nav__logo {
  width: 96px;
  height: 18px;
  margin-left: 3%;
  margin-right: auto;

  &--opened {
    margin-bottom: 59px;

    .main-nav__logo-image--white {
      display: none;
    }
  }

  &--closed {
    margin-bottom: 33px;

    .main-nav__logo-image--blue {
      display: none;
    }
  }
}

.main-nav__logo-image {
  width: 100%;
  height: 100%;
}

.main-nav--closed .main-nav__toggle {
  .indents-reset;
  position: relative;
  width: 10%;
  height: 11px;
  margin-top: 4px;

  cursor: pointer;

  border: none;
  background-color: transparent;

  &::before {
    .page-header__button-pseudo;
    top: 0;
  }

  &::after {
    .page-header__button-pseudo;
    bottom: 0;
  }

  .main-nav__close-icon {
    display: none;
  }
}

.main-nav--opened .main-nav__toggle {
  display: block;
  width: 19px;
  height: 19px;
  padding: 0;

  background-color: transparent;
  border: none;

  cursor: pointer;
}

.main-nav__close-icon {
  fill: @nav-bth-color;
}

.main-nav__list {
  &--site-nav {
    margin-bottom: 52px;
    margin-left: 15%;
    margin-right: 13%;
  }

  &--login {
    margin: 0;
    margin-bottom: 63px;
    margin-left: 2%;
  }

  &--menu-contacts {
    margin: 0;
    margin-bottom: 53px;
  }

  &--social {
    margin: 0;
    margin-left: 29%;
  }
}

@media (min-width: @tablet-width) {
  .main-nav {
    &::after {
      content: none;
    }

    &--nojs {
      .user-nav--menu {
        display: none;
      }
    }

    &--opened {
      top: 62px;

      .main-nav__list--login {
        display: block;
        margin-left: 20%;
        order: -1;
      }
    }

    &--closed {
      .main-nav__list--login {
        display: block;
        margin-left: 13%;
      }
    }

    &--scroll-menu {
      .main-nav__wrapper {
        padding-bottom: 10px;
      }

      .main-nav__list--login {
        margin-bottom: 0;
      }
    }
  }

  .main-nav--opened.main-nav--catalog {
    top: 0;
  }

  .main-nav--closed .main-nav__toggle {
    margin-top: 21px;
  }

  .main-nav--opened .main-nav__toggle {
    margin-top: 17px;
    padding-left: 3px;

    order: -2;
  }

  .main-nav__wrapper {
    width: 95.9%;
    margin-left: 2.5%;
    padding-top: 40px;
    padding-bottom: 73px;

    &--catalog {
      padding-bottom: 50px;
    }

    &--form {
      padding-bottom: 50px;
    }
  }

  .main-nav__logo {
    width: 164px;
    height: 42px;
    margin-right: 22.5%;
    padding-top: 6px;
    order: -4;
  }

  .main-nav__list {
    &--site-nav {
      margin-left: 10.3%;
      margin-top: 32px;
      margin-bottom: 155px;
    }

    &--menu-contacts {
      margin-bottom: 149px;
    }

    &--social {
      margin-left: 20%;
    }
  }

  .main-nav__close {
    order: -3;
    margin-right: 21%;
    margin-top: 15px;
  }
}

@media (min-width: @desktop-width) {
  .main-nav {
    position: relative;
    padding: 0;

    background-color: transparent;

    &--scroll-menu {
      background-color: @clear-bg;

      .main-nav__logo {
        &::after {
          background-color: @main-dark-color;
        }
      }
    }
  }

  .main-nav--closed.main-nav--scroll-menu {
    .main-nav__toggle {
      display: none;
    }

    .main-nav__list--login {
      top: -109px;
    }
  }

  .main-nav__wrapper {
    width: 92%;
    max-width: 1462px;
    margin-left: 4%;
    margin-right: auto;
    padding-left: 0;
    padding-top: 49px;
    display: flex;
    flex-wrap: wrap;
  }

  .main-nav__list--site-nav {
    display: flex;
    align-self: flex-start;
  }

  .main-nav__logo {
    position: relative;
    height: 52px;
    width: 200px;
    margin: 0;
    padding: 0;
    margin-top: -2px;
    margin-right: 10.9%;
    padding-left: 2px;

    &::after {
      content: "";
      position: absolute;
      right: -49%;
      top: 26px;
      display: block;
      width: 60px;
      height: 2px;

      background-color: @clear-bg;
    }

    &--catalog {
      margin-right: 8%;
    }

    &--form {
      margin-right: 8%;

      &::after {
        right: -50%;
      }
    }
  }

  .main-nav--closed .main-nav__list--login {
    display: none;
  }

  .main-nav__toggle {
    display: none;
  }
}
