.profile-add {
  box-sizing: border-box;
  position: relative;
  z-index: 3;

  background-color: @light-bg;

  border-bottom-left-radius: 27px;
  border-bottom-right-radius: 27px;
}

.profile-add__wrapper {
  box-sizing: border-box;
  width: 84%;
  margin-left: auto;
  margin-right: auto;
  padding-top: 5px;
  padding-left: 7.4%;
  padding-bottom: 38px;
}

.profile-add__title {
  .indents-reset;
  width: 52%;
  max-width: 147px;
  margin-bottom: 43px;

  .title-font;
  color: @index-section-title;
}

.profile-add__rates {
  margin-bottom: 31px;
}

.profile-add__show-all {
  display: block;
  .bold-font;
  line-height: 16px;
  color: @third-font-button-color;

  word-wrap: break-word;

  cursor: pointer;

  .profile-add__show-all-icon {
    margin-left: 36%;
    margin-top: 22px;
    fill: @third-font-button-color;
  }

  &:hover {
    color: @third-font-button-color-hover;

    .profile-add__show-all-icon {
      fill: @third-font-button-color-hover;
    }
  }

  &:active {
    color: @third-font-button-color-active;

    .profile-add__show-all-icon {
      fill: @third-font-button-color-active;
    }
  }
}

.profile-add__icon-button-container {
  display: none;

  background-color: @bg-country-filter;
}

.profile-add__business-tariffs {
  margin-left: 8%;
  margin-right: auto;
}

.profile-add__show-tariffs:checked ~ .profile-add__business-tariffs {
  display: block;
}

.profile-add__hidden-tariffs:checked ~ .profile-add__business-tariffs {
  display: none;
}

@media (min-width: @tablet-width) {
  .profile-add {
    border-bottom-left-radius: 60px;
    border-bottom-right-radius: 55px;
  }

  .profile-add__wrapper {
    width: 89%;
    padding: 0;
    padding-bottom: 66px;
  }

  .profile-add__title {
    width: 71%;
    max-width: inherit;
    margin-left: 15%;
    margin-right: auto;
    margin-bottom: 74px;
    font-size: 45px;
    line-height: 45px;
  }

  .profile-add__show-all {
    display: block;
    width: 65%;
    align-self: center;

    font-size: 20px;
    line-height: 20px;
  }

  .profile-add__icon-button-container {
    box-sizing: border-box;
    display: inline-block;
    width: 67px;
    height: 67px;
    margin-right: 12px;

    border-radius: 50%;
  }

  .profile-add__rates {
    margin-bottom: 29px;
  }
}

@media (min-width: @desktop-width) {
  .profile-add__wrapper {
    width: 91%;
    max-width: 1462px;
    padding: 0;
    padding-top: 33px;
    padding-bottom: 167px;
  }

  .profile-add__title {
    width: 40%;
    margin-left: 31.7%;
    margin-bottom: 133px;
  }

  .profile-add__show-all {
    margin-top: 30px;
    margin-left: 67px;
  }

  .profile-add__icon-button-container {
    margin-right: 1.8%;
  }
}
