.filter-countries {
  box-sizing: border-box;

  background-color: @bg-country-filter;
  border-bottom-left-radius: 20px;
  border-bottom-right-radius: 20px;
}

.filter-countries__title {
  margin: 0;
  margin-right: 7%;
  order: -2;
}

.filter-countries__show-button {
  order: -1;
}

.filter-countries__wrapper {
  box-sizing: border-box;
  position: relative;
  width: 84%;
  margin-left: auto;
  margin-right: auto;
  margin-top: -17px;
  padding-top: 27px;
  padding-bottom: 12px;

  display: flex;
  flex-wrap: wrap;

  &::before {
    content: none;
    position: absolute;
    left: -10%;
    top: 0;
    z-index: -1;
    width: 100vw;
    height: 100%;

    background: @bg-country-filter;
    background-size: 100%;
  }
}

.filter-countries__continents-list {
  position: relative;
  top: 30px;
  left: 25px;
  margin: 0;
  padding: 0;
}

.filter-countries__check ~ .filter-countries__wrapper {
  .filter-countries__show-button {
    .show-filter-toggle__close-icon {
      display: none;
    }

    .show-filter-toggle__show-icon {
      display: block;
    }
  }

  .filter-countries__continents-list {
    display: none;
  }

  .filter-expended {
    display: none;
  }
}

.filter-countries__check:focus ~ .filter-countries__wrapper {
  .filter-countries__show-button {
    outline: rgb(59, 153, 252) auto 5px;
  }
}

.filter-countries__check:checked {
  & ~ .filter-countries__wrapper {
    position: absolute;
    left: 25px;
    top: 142px;
    z-index: 2;

    &::before {
      content: "";
    }

    .filter-countries__show-button {
      .show-filter-toggle__close-icon {
        display: block;
      }

      .show-filter-toggle__show-icon {
        display: none;
      }
    }

    .filter-expended {
      display: block;
    }

    .filter-countries__continents-list {
      display: flex;
    }
  }
}

@media (min-width: @tablet-width) {
  .filter-countries {
    margin-top: -60px;

    border-bottom-left-radius: 60px;
    border-bottom-right-radius: 60px;
  }

  .filter-countries__wrapper {
    padding-top: 119px;
    padding-bottom: 54px;

    &::before {
      border-bottom-left-radius: 8%;
      border-bottom-right-radius: 8%;
    }
  }

  .filter-countries__check ~ .filter-countries__wrapper {
    .filter-countries__continents-list {
      display: flex;
    }

    .filter-countries__show-button {
      .show-filter-toggle__text-hidden {
        display: none;
      }

      .show-filter-toggle__text-show {
        display: block;
      }
    }
  }

  .filter-countries__check:checked ~ .filter-countries__wrapper {
    left: 61px;
    top: 307px;

    .filter-countries__continents-list {
      display: flex;
    }

    .filter-expended {
      display: flex;
    }

    .filter-countries__show-button {
      .show-filter-toggle__text-hidden {
        display: block;
      }

      .show-filter-toggle__text-show {
        display: none;
      }
    }
  }

  .filter-countries__continents-list {
    position: relative;
    left: 11px;
    top: 1px;
    margin-right: 61px;
    order: -3;
  }
}

@media (min-width: @desktop-width) {
  .filter-countries {
    margin-top: -68px;
  }

  .filter-countries__wrapper {
    width: 92%;
    max-width: 1462px;
    margin-top: 0;
    padding-top: 127px;
    padding-bottom: 30px;
  }

  .filter-countries__title {
    display: block;
    margin-left: 21px;
    margin-right: 0;
    order: -4;
  }

  .filter-countries__check:checked ~ .filter-countries__wrapper {
    position: relative;
    left: 0;
    top: 0;

    &::before {
      left: -5%;
      width: 76vw;
      max-width: 1462px;
      background-color: @light-bg;

      border-bottom-left-radius: 0;
      border-bottom-right-radius: 0;
    }

    &::after {
      content: "";
      position: absolute;
      top: 0;
      left: 0;
      z-index: -1;
      display: block;
      width: 96vw;
      max-width: 1462px;
      height: 100%;
      right: -14.3%;
      background-color: @light-bg;

      border-bottom-left-radius: 0;
      border-bottom-right-radius: 0;
    }
  }

  .filter-countries__continents-list {
    left: 0;
    margin-left: 0;
    margin-right: auto;
  }

  .filter-countries__show-button {
    margin-right: 1%;
  }
}
