.location {
  position: relative;
  z-index: 2;
  margin-top: -25px;

  background-color: @yellow-button-color;
  border-bottom-left-radius: 30px;
  border-bottom-right-radius: 30px;
}

.location__wrapper {
  position: relative;
  width: 84%;
  margin-left: auto;
  margin-right: auto;
  padding-top: 57px;
  padding-bottom: 35px;
}

.location__paragraph {
  .indents-reset;
  width: 77%;
  margin-bottom: 30px;
  padding-left: 23%;
  padding-top: 2px;

  .bold-font;
  color: @font-yellow-button;

  word-wrap: break-word;

  text-transform: uppercase;
}

.location__button {
  display: block;
  box-sizing: border-box;
  width: 100%;
  padding-top: 20px;
  padding-bottom: 19px;
  padding-left: 7%;
  padding-right: 1%;

  .bold-font;
  font-size: 18px;
  line-height: 18px;
  color: @font-yellow-button;

  border: 3px solid @font-yellow-button;
  border-radius: 10px;

  word-wrap: break-word;

  .location__button-icon {
    margin-left: 17.5%;
    fill: @font-yellow-button;
  }

  &:hover {
    background-color: @main-dark-color;
    color: @clear-font;

    border: 3px solid @main-dark-color;

    .location__button-icon {
      fill: @clear-bg;
    }
  }

  &:active {
    color: @font-color-value;

    .location__button-icon {
      fill: @clear-bg-opac-30;
    }
  }
}

.location__icon-wrapper {
  position: absolute;
  box-sizing: border-box;
  display: inline-block;
  width: 45px;
  height: 45px;
  margin-right: 4%;
  padding-top: 13px;
  padding-left: 14px;

  border-radius: 50%;
  background-color: @dark-icon-bg;

  .location__icon {
    width: 14px;
    height: 22px;

    fill: @yellow-button-color;
  }
}

@media (min-width: @tablet-width) {
  .location {
    margin-top: -60px;

    border-bottom-left-radius: 56px;
    border-bottom-right-radius: 60px;
  }

  .location__wrapper {
    margin-left: auto;
    margin-right: auto;
    padding-top: 107px;
    padding-left: 8.3%;
    padding-right: 5%;
    padding-bottom: 85px;
  }

  .location__paragraph {
    .indents-reset;
    margin-bottom: 61px;
    padding-left: 14.5%;
    padding-top: 22px;

    font-size: 28px;
    line-height: 28px;

    text-transform: uppercase;

    & br {
      display: none;
    }
  }

  .location__button {
    width: 96%;
    padding-top: 28px;
    padding-bottom: 23px;
    padding-left: 5%;

    font-size: 24px;
    line-height: 24px;

    border-radius: 20px;

    .location__button-icon {
      margin-left: 53.5%;
    }
  }

  .location__icon-wrapper {
    width: 68px;
    height: 68px;
    margin-right: 2%;
    padding-top: 21px;
    padding-left: 2.8%;

    .location__icon {
      width: 27px;
      height: 27px;
    }
  }
}

@media (min-width: @desktop-width) {
  .location__wrapper {
    width: 74%;
    max-width: 1462px;
    margin-left: auto;
    margin-right: auto;
    padding-top: 114px;
    padding-bottom: 60px;
    padding-left: 261px;
    padding-right: 5%;

    display: flex;
  }

  .location__icon-wrapper {
    width: 90px;
    height: 90px;
    margin: 0;
    padding-top: 26px;
    padding-left: 30px;

    .location__icon {
      width: 28px;
      height: 43px;
    }
  }

  .location__paragraph {
    .indents-reset;
    width: 41%;
    margin-bottom: 0;
    padding-top: 31px;
    padding-left: 122px;

    font-size: 28px;
    line-height: 28px;

    text-transform: uppercase;
  }

  .location__button {
    width: 326px;
    padding: 0;
    margin-top: 5px;
    margin-left: 140px;
    padding-top: 27px;
    padding-bottom: 24px;
    padding-left: 33px;
    align-self: start;

    font-size: 24px;
    line-height: 24px;

    .location__button-icon {
      margin-left: 5.1%;
    }
  }
}
