.subscription-form {
  position: relative;
  box-sizing: border-box;
  width: 100%;
  height: 60px;
}

.subscription-form__input {
  box-sizing: border-box;
  width: 100%;
  height: 100%;
  padding-left: 8.3%;
  padding-right: 24%;

  .bold-font;
  font-size: 24px;
  line-height: 24px;

  border: none;
  border-radius: 10px;
  background-color: @clear-bg;

  outline: none;

  &::placeholder {
    .bold-font;
    font-size: 18px;
    line-height: 18px;
    color: @no-active-basic-font-15proc-up;
    word-wrap: break-word;
  }

  &:hover {
    background-color: @light-bg;

    &::placeholder {
      color: @font-color-value-20proc-up;

      opacity: 1;
    }
  }

  &:active {
    background-color: @clear-bg;
  }

  &:focus {
    font-size: 24px;
    line-height: 24px;
    color: @clear-font;

    background-color: @dark-bg;
    border: 4px solid @clear-bg;

    &::placeholder {
      color: @clear-bg;

      opacity: 1;
    }
  }
}

.subscription-form__input--invalid {
  color: @invalid-font;
  background-color: @invalid-form;
}

.subscription-form__submit-button {
  position: absolute;
  right: 0;
  width: 60px;
  padding-top: 20px;
  padding-bottom: 20px;

  background-color: @yellow-button-color;
  border: none;
  border-radius: 10px;

  cursor: pointer;

  & span {
    display: none;

    .bold-font;
    font-size: 24px;
    line-height: 24px;
    color: @font-yellow-button;
  }

  .subscription-form__submit-button-icon {
    margin-left: 11%;
    margin-top: 3px;
  }
}

@media (min-width: @tablet-width) {
  .subscription-form {
    width: 94.8%;
    height: 81px;
  }

  .subscription-form__input {
    padding-left: 5.8%;

    font-size: 24px;
    line-height: 24px;

    border-radius: 20px;

    &::placeholder {
      font-size: 24px;
      line-height: 24px;
    }
  }

  .subscription-form__submit-button {
    width: 13%;
    height: 100%;
    border-top-left-radius: 0;
    border-bottom-left-radius: 0;
    border-top-right-radius: 21px;
    border-bottom-right-radius: 21px;

    cursor: pointer;

    .subscription-form__submit-button-icon {
      margin: 0;
      fill: @font-yellow-button;
    }

    .button-actions;
  }
}

@media (min-width: @desktop-width) {
  .subscription-form {
    width: 100%;
  }

  .subscription-form__input {
    padding-left: 3%;
  }

  .subscription-form__submit-button {
    width: 27.5%;
    padding: 0;
    padding-top: 18px;

    & span {
      padding-left: 11%;
      display: block;

      text-align: left;
    }

    .subscription-form__submit-button-icon {
      position: relative;
      left: 120px;
      top: -18px;
    }
  }
}
