.users {
  box-sizing: border-box;
  width: 270px;
  padding: 0;

  list-style: none;
}

.users__item {
  box-sizing: border-box;
  position: relative;
  width: 100%;
  margin-left: auto;
  margin-right: auto;
  margin-bottom: 10px;
  padding-bottom: 15px;
  display: flex;
  flex-direction: column;

  background-color: @clear-bg;
  border-radius: 10px;

  &:hover {
    box-shadow: 0 6px 21px 0 rgba(0, 0, 0, 0.08);
  }
}

.users__level {
  position: absolute;
  top: 16px;
  left: 6.5%;

  &--demin {
    top: 14px;
    left: 5.5%;
  }
}

.users__flags {
  width: 53%;
  margin-top: -18px;
  margin-bottom: 14px;
  margin-left: auto;
  margin-right: auto;
  order: -2;

  &--demin {
    margin-top: -20px;
    margin-bottom: 16px;
  }
}

.users__title {
  .indents-reset;
  position: relative;
  width: 50%;
  margin-bottom: 15px;
  margin-left: 26%;
  margin-right: auto;

  .bold-font;
  font-size: 21px;
  line-height: 21px;
  color: @users-title-color;

  word-wrap: break-word;

  &--demin {
    margin-left: 29%;
    margin-bottom: 13px;
  }

  &::before {
    position: absolute;
    left: -12.5%;
    top: 6px;
    display: block;
    width: 5px;
    height: 5px;
    content: "";

    background-color: @offline-user-color;
    border-radius: 50%;
  }
}

.users__title-link {
  &:hover {
    opacity: 0.7;
  }

  &:active {
    opacity: 0.3;
  }
}

.users__title--demin::before {
  background-color: @online-user-color;
}

.users__avatar {
  order: -3;
}

.users__img {
  width: 100%;
  height: 270px;

  border-top-left-radius: 10px;
  border-top-right-radius: 10px;
}

.users__hashtags {
  margin-left: 25px;
  margin-right: auto;
  margin-bottom: 17px;

  &--demin {
    margin-left: 42px;
  }
}

.users__transportation-list {
  margin-left: 74px;
  margin-right: auto;
}

@media (min-width: @tablet-width) {
  .users {
    width: 99.8%;
    padding: 0 4.8%;
    display: flex;
    flex-wrap: wrap;
  }

  .users__item {
    width: 285px;
    padding-bottom: 22px;
    align-self: flex-start;

    border-radius: 18px;

    &:nth-of-type(2n+1) {
      margin-left: 0;
      margin-right: auto;
    }

    &:nth-of-type(2n) {
      margin-left: 0;
      margin-right: 0;
    }
  }

  .users__img {
    width: 100%;
    height: 285px;

    border-top-left-radius: 18px;
    border-top-right-radius: 18px;
  }

  .users__level {
    top: 20px;
    left: 7%;
  }

  .users__flags {
    margin-left: 25%;
    margin-bottom: 19px;

    &--demin {
      margin-top: -19px;
      margin-left: 34.5%;
    }
  }

  .users__title {
    width: 64%;
    margin-bottom: 21px;
    margin-left: 18%;

    font-size: 24px;
    line-height: 24px;

    text-align: center;

    &::before {
      position: absolute;
      left: -2.5%;
      top: 7px;
      width: 7px;
      height: 7px;
    }

    &--demin {
      &::before {
        left: 1.5%;
        top: 8px;
      }
    }
  }

  .users__transportation-list {
    margin-left: 85px;
  }

  .users__hashtags {
    margin-left: 11%;
    margin-right: 10.5%;
    margin-bottom: 24px;
  }
}

@media (min-width: @desktop-width) {
  .users {
    width: 50.5%;
    margin-bottom: 0;
    padding: 0;
    padding-top: 5px;
    display: flex;
    flex-wrap: wrap;
  }

  .users__item {
    width: 45.1%;
    margin: 0;
    padding: 0;
    padding-bottom: 22px;
    align-self: flex-start;
  }

  .users__item:first-of-type {
    margin-right: 9.4%;
  }

  .users__level {
    position: absolute;
    top: 23px;
    left: 8%;
  }

  .users__title {
    margin-bottom: 20px;

    &--demin {
      margin-bottom: 19px;
    }
  }

  .users__hashtags {
    width: 78%;
  }

  .users__flags {
    &--demin {
      margin-bottom: 21px;
    }
  }
}
