.input-value-field {
  box-sizing: border-box;
  width: 100%;
  padding: 0;
  padding-top: 15px;
  padding-bottom: 8px;
  display: flex;
  justify-content: space-between;

  border: 1px solid @border-color;
  border-radius: 6px;

  &:hover {
    border-color: @level-range-border-hover;
  }

  &:active {
    border-color: @active-border;
  }
}


.input-value-field__button {
  box-sizing: border-box;
  position: relative;

  background-color: transparent;
  border: none;

  cursor: pointer;

  svg {
    position: absolute;
    fill: @font-color-value-10proc-down;
  }

  &:hover {
    svg {
      fill: @font-color-value-10proc-up;
    }
  }

  &:active {
    svg {
      fill: @font-color-value-20proc-down;
    }
  }
}

.input-value-field__decrease-icon {
  top: 5px;
  right: -15px;
}

.input-value-field__increase-icon {
  top: -1px;
  right: 13px;
}

.input-value-field__input-value {

  .medium-font;
  font-size: 15px;
  line-height: 13px;
  color: @primary-dark-font-color;

  text-align: center;

  border: none;
}

@media (min-width: @tablet-width) {
  .input-value-field {
    padding-top: 21px;
    padding-bottom: 20px;
  }

  .input-value-field__button {
    width: 19%;

    &--decrease {
      svg {
        top: 13px;
        right: 57%;
        width: 20px;
      }
    }

    &--increase {
      svg {
        top: 4px;
        right: 18%;
        width: 20px;
        height: 20px;
      }
    }
  }

  .input-value-field__input-value {
    margin-top: 3px;

    font-size: 20px;
    line-height: 18px;
  }
}

@media (min-width: @desktop-width) {
  .input-value-field {
    width: 36%;
  }

  .input-value-field__button {
    width: 20%;
    padding: 0;

    svg {
      top: 12px;
      right: -2px;
    }

    .input-value-field__increase-icon {
      right: 20px;
      top: 1px;
    }
  }

  .input-value-field__input-value {
    width: 40%;
  }
}
