.transportation-area {
  box-sizing: border-box;
  position: relative;
  width: 100%;
  min-height: 63px;
  padding-top: 14px;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;

  border: 1px solid @border-color;
  border-radius: 10px;
}

.transportation-area__title {
  position: relative;
  top: -22px;
  right: -2%;
  .indents-reset;
  width: 43%;

  .bold-font;
  font-size: 16px;
  line-height: 18px;
  color: @primary-dark-font-color;
  text-align: center;

  text-transform: uppercase;

  background-color: @form-bg;
}

@media (min-width: @tablet-width) {
  .transportation-area {
    box-sizing: border-box;
    width: 49.5%;
    padding-top: 26px;
    padding-bottom: 24px;
    align-self: flex-start;
  }

  .transportation-area__title {
    top: 61px;
    left: 6%;

    .medium-font;
    font-size: 20px;
    line-height: 18px;
    letter-spacing: 1.5px;
  }
}

@media (min-width: @desktop-width) {
  .transportation-area__title {
    top: 60px;
    left: 0;
  }

  .transportation-area {
    width: 30.6%;
    height: 100%;
    padding-left: 9px;
    padding-bottom: 23px;
  }
}
