.steps-list {
  .list-res;
}

.steps-list__item {
  padding-top: 24px;
  padding-left: 8%;
  padding-right: 8%;
  padding-bottom: 10px;

  background-color: @clear-font;

  border-radius: 10px;

  &:not(:last-of-type) {
    margin-bottom: 10px;
  }
}

.steps-list__first-step-header {
  margin-bottom: 20px;
  padding-left: 1%;
  padding-right: 1%;

  &--step3 {
    margin-bottom: 22px;
  }
}

.steps-list__title {
  .indents-reset;
  margin-bottom: 16px;

  .bold-font;
  font-size: 20px;
  line-height: 20px;
  color: @primary-dark-font-color;

  word-break: break-all;

  &--step3 {
    margin-bottom: 14px;
  }

  span {
    display: block;
  }
}

.steps-list__container-input-value {
  padding-left: 1%;
  padding-right: 1%;
}

.steps-list__paragraph {
  .indents-reset;
  .regular-font;

  br {
    display: none;
  }
}

.steps-list__value-wrapper {
  margin-bottom: 20px;
}

.steps-list__calendar {
  margin-bottom: 22px;
  padding-left: 1%;
  padding-right: 1%;
}

.steps-list__next-step-button,
.steps-list__submit-button {
  box-sizing: border-box;
  display: block;
  width: 100%;
  margin-bottom: 12px;
  padding-top: 6px;
  padding-bottom: 4px;
  padding-left: 19%;

  .bold-font;
  font-size: 17px;
  line-height: 30px;
  letter-spacing: 1.1px;
  color: @secondary-color-font-button;

  word-break: break-all;

  text-transform: uppercase;

  background-color: @yellow-button-color;
  border: none;
  border-radius: 20px;

  cursor: pointer;

  .steps-list__step-icon {
    display: none;

    fill: @primary-dark-font-color;
  }

  .button-actions;
}

.steps-list__submit-button {
  padding-left: 1%;

  letter-spacing: inherit;
}

.steps-list__select-country {
  margin-bottom: 22px;
}

.steps-list__back-step {
  display: block;
  .bold-font;
  font-size: 17px;
  line-height: 30px;
  color: @secondary-color-font-button;

  word-break: break-all;

  text-transform: uppercase;

  text-align: center;

  .steps-list__step-icon {
    display: none;

    fill: @primary-dark-font-color;
    transform: rotate(180deg);
  }

  &:hover {
    color: @primary-dark-font-color-opac-70;

    .steps-list__step-icon {
      fill: @primary-dark-font-color-opac-70;
    }
  }

  &:active {
    color: @primary-dark-font-active;

    .steps-list__step-icon {
      fill: @primary-dark-font-active;
    }
  }
}

.steps-list__selected-country-wrapper {
  &--secondary {
    margin-bottom: 15px;
  }

  position: relative;
  padding-left: 1%;
  padding-right: 1%;
}

.steps-list__selected-country-title {
  position: relative;
  display: block;
  width: 100%;

  word-break: break-all;

  &::after {
    content: "";
    position: absolute;
    right: 0;
    top: -4px;
    z-index: 1;

    display: block;
    width: 35px;
    height: 24px;

    box-shadow: -35px 0 2px 0 @box-shadow-title-country;
    background-size: contain;
    background-repeat: no-repeat;
  }

  &--bosni-gerc::after {
    background-image: url("../img/flag-bosnia.svg");
  }

  &--czech::after {
    background-image: url("../img/flag-czech.svg");
  }
}

.steps-list__textarea {
  box-sizing: border-box;
  position: relative;
  width: 100%;
  height: 220px;
  margin-bottom: 20px;
  padding-top: 9px;
  padding-bottom: 12px;
  padding-left: 6.8%;
  padding-right: 8%;

  .regular-font;
  font-size: 16px;
  line-height: 22px;

  resize: none;

  border-radius: 5px;
  border: 1px solid @border-color;

  &--secondary {
    margin: 0;
  }

  &:hover {
    border-color: @level-range-border-hover;
  }

  &:active,
  &:focus {
    border-color: @border-color-active;
  }

  &:required {
    border-color: @invalid-text-area;
  }

  &::placeholder {
    color: @no-active-basic-font-15proc-up;
  }
}

.steps-list__first-textarea-wrapper {
  &::before {
    content: "";
    position: absolute;
    top: 10px;
    left: -4.3%;
    display: block;
    width: 2px;
    height: 100%;

    background-color: @primary-dark-font-color;
  }
}

.steps-list__selected-country {
  position: relative;
  margin: 0;
  margin-bottom: 17px;
  padding-left: 13px;

  .medium-font;
  font-size: 18px;
  line-height: 18px;
  color: @primary-dark-font-color;

  &::after {
    content: "";
    position: absolute;
    left: -1%;
    bottom: 6px;
    display: block;
    width: 6px;
    height: 6px;

    background-color: @select-country-pseudo;
    border-radius: 50%;
  }

  &::before {
    content: "";
    position: absolute;
    left: -5.5%;
    top: 8px;
    display: block;
    width: 4.9%;
    height: 2px;

    background-color: @select-country-pseudo;
  }
}

.steps-list__wrapper-select-country-list {
  width: 100%;
  margin-bottom: 25px;
  padding-left: 1%;
  padding-right: 1%;
}

@media (min-width: @tablet-width) {
  .steps-list__item {
    padding-top: 49px;
    padding-bottom: 42px;
    padding-left: 7.4%;
    padding-right: 7.4%;

    border-radius: 30px;

    &:first-of-type {
      margin-bottom: 40px;
    }

    &:nth-of-type(2) {
      margin-bottom: 51px;
      padding-bottom: 41px;
      display: flex;
      flex-wrap: wrap;
    }

    &:nth-of-type(3) {
      padding-top: 48px;
      padding-bottom: 40px;
    }
  }

  .steps-list__title {
    width: 70%;
    margin-bottom: 33px;

    font-size: 36px;
    line-height: 36px;

    span {
      display: block;
    }
  }

  .steps-list__paragraph {
    margin: 0;
    width: 95%;

    font-size: 20px;
    line-height: 24px;

    &--step2 {
      width: 78%;
    }
  }

  .steps-list__first-step-header {
    margin-bottom: 38px;
    padding: 0;

    &--step2 {
      margin-bottom: 34px;
    }

    &--step3 {
      margin-bottom: 33px;
    }
  }

  .steps-list__container-input-value {
    margin-bottom: 17px;
    padding: 0;
    display: flex;
    flex-direction: column;
  }

  .steps-list__children-check {
    margin-bottom: 32px;
    order: -1;
  }

  .steps-list__value-wrapper {
    &:first-of-type {
      order: -2;
    }

    &:nth-of-type(2) {
      padding-top: 27px;
    }
  }

  .steps-list__label {
    font-size: 20px;
    line-height: 18px;
    color: @primary-dark-font-color;

    text-transform: uppercase;
  }

  .steps-list__calendar {
    margin-bottom: 46px;
    padding: 0;
  }

  .steps-list__next-step-button {
    display: inline-block;
    width: 54%;
    margin-right: auto;
    padding-left: 6%;
    padding-top: 28px;
    padding-bottom: 28px;

    font-size: 24px;
    line-height: 24px;

    text-transform: none;

    .steps-list__step-icon {
      display: inline-block;
      margin-left: 11%;
    }
  }

  .steps-list__wrapper-select-country-list {
    margin-bottom: 50px;
    padding: 0;

    border-top: 1px solid @form-border-second-value;
  }

  .steps-list__back-step {
    display: inline-block;
    width: 36%;
    padding-top: 28px;

    font-size: 24px;
    line-height: 24px;

    text-transform: none;
    text-align: right;

    .steps-list__step-icon {
      display: inline-block;
      position: relative;
      left: -7%;
    }
  }

  .steps-list__selected-country {
    margin: 0;
    padding-top: 42px;
    padding-left: 0;

    font-size: 30px;
    line-height: 18px;

    &::after {
      bottom: 0;
      left: 98%;
      width: 15px;
      height: 15px;
    }

    &::before {
      content: none;
    }

    &--bosni-gerc {
      margin-bottom: 44px;

      border-top: 1px solid @form-border-second-value;
    }

    &--chech {
      padding-top: 51px;
      margin-bottom: 43px;
    }
  }

  .steps-list__selected-country-title {
    &::after {
      right: 5.5%;
      top: -14px;
      width: 73px;
      height: 49px;
    }
  }

  .steps-list__selected-country-wrapper {
    padding: 0;

    &:last-of-type {
      margin-bottom: 41px;
    }
  }

  .steps-list__submit-button {
    display: inline-block;
    width: 53.4%;
    margin-right: 9.7%;
    padding-top: 28px;
    padding-bottom: 28px;
    padding-left: 6%;
    text-align: left;

    font-size: 24px;
    line-height: 24px;

    text-transform: none;

    .steps-list__step-icon {
      display: inline-block;
      margin-left: 35%;
    }
  }

  .steps-list__textarea {
    width: 94.5%;
    height: 142px;
    margin: 0;
    padding-top: 18px;
    padding-left: 3%;
    padding-right: 8%;

    font-size: 20px;
    line-height: 24px;

    border: 1px solid @border-color;
    border-radius: 5px;
  }

  .steps-list__first-textarea-wrapper {
    &::before {
      left: 99%;
      top: 60px;
      width: 3px;
    }
  }
}

@media (min-width: @desktop-width) {
  .steps-list__item {
    padding-top: 100px;
    padding-bottom: 92px;
    padding-left: 11.8%;
    padding-right: 11.8%;
    display: flex;
    flex-wrap: wrap;

    border-radius: 60px;

    &:not(:last-of-type) {
      margin-bottom: 45px;
    }

    &:nth-of-type(2) {
      padding-bottom: 92px;
    }

    &:nth-of-type(3) {
      padding-top: 100px;
      padding-bottom: 94px;
    }
  }

  .steps-list__first-step-header {
    margin-bottom: 55px;
    padding-left: 3px;
    padding-right: 1%;
    display: flex;
    flex-wrap: wrap;

    &--step2 {
      margin-bottom: 52px;
      display: flex;
      flex-direction: row;
    }

    &--step3 {
      margin-bottom: 52px;
    }
  }

  .steps-list__title {
    width: 54%;
    order: -2;

    &--step2,
    &--step3 {
      margin-right: 279px;
      margin-bottom: 0;
      align-self: flex-start;
    }

    span {
      display: inline-block;
    }
  }

  .steps-list__paragraph {
    width: 60%;

    br {
      display: block;
    }

    &--step2,
    &--step3 {
      width: 41%;
      margin-top: -51px;
      padding-top: 2px;
      padding-left: 1px;
    }

    &--step3 {
      width: 50%;
    }
  }

  .steps-list__step-link {
    margin-top: 9px;

    order: -1;
  }

  .steps-list__container-input-value {
    width: 100%;
    margin-bottom: 0;
    padding-left: 3px;
    flex-direction: row;
    flex-wrap: wrap;

    border-top: 1px solid @form-border-second-value;
  }

  .steps-list__value-wrapper {
    width: 37%;
    margin-bottom: 0;
    align-self: flex-start;

    &:nth-of-type(2) {
      padding-top: 13px;
    }
  }

  .steps-list__children-check {
    width: 27%;
    margin-left: auto;
    margin-bottom: 0;
    padding-top: 44px;
  }

  .steps-list__calendar {
    margin-bottom: 56px;
  }

  .steps-list__wrapper-select-country-list {
    margin-bottom: 55px;
    padding-left: 4px;
    padding-right: 4px;
  }

  .steps-list__next-step-button {
    width: 28.5%;
    margin-left: 3px;
    padding-left: 35px;
    padding-top: 30px;
  }

  .steps-list__back-step {
    padding-top: 30px;
    padding-right: 7px;

    .steps-list__step-icon {
      left: -16px;
    }
  }

  .steps-list__selected-country-wrapper {
    width: 100%;

    &--secondary {
      padding-left: 4px;
      padding-right: 4px;
    }

    &:last-of-type {
      margin-bottom: 50px;
    }
  }

  .steps-list__first-textarea-wrapper::before {
    top: 90px;
    left: 77%;
    height: 90%;
  }

  .steps-list__textarea {
    width: 73%;
    height: 142px;
    padding-top: 24px;
    padding-left: 25px;
    padding-right: 10px;
  }

  .steps-list__selected-country {
    padding-top: 85px;
    padding-left: 4px;

    &--chech {
      padding-left: 0;
      padding-top: 51px;
    }

    &::after {
      left: 76.5%;
    }
  }

  .steps-list__selected-country--chech::after {
    left: 76.6%;
  }

  .steps-list__selected-country-title::after {
    right: 115px;
    top: -12px;
    box-shadow: none;
  }

  .steps-list__selected-country-title--chech::after {
    right: 114px;
  }

  .steps-list__first-textarea-wrapper {
    padding-left: 4px;
    padding-right: 4px;
  }

  .steps-list__submit-button {
    width: 28.5%;
    margin-left: 4px;
    margin-right: auto;
    padding-left: 33px;
  }
}
