.filter-expended {
  box-sizing: border-box;
  width: 100%;
  padding-top: 50px;
  padding-bottom: 15px;

  &--roll-up {
    display: none;
  }
}

.filter-expended__title {
  margin: 0;
}

.filter-expended__wrapper-title {
  box-sizing: border-box;
  width: 100%;
  margin-bottom: 37px;
  padding-top: 41px;

  display: flex;
  justify-content: space-between;
}

.filter-expended__close-button {
  margin-right: 2.5%;
}

.filter-expended__alphabet {
  margin-left: 25px;
  margin-right: auto;
  margin-bottom: 13px;
}

.filter-expended__continents {
  margin-bottom: 25px;
  margin-left: 9%;
}

.filter-expended__continents-list {
  margin: 0;

  &--header-filter {
    display: none;
  }
}

.filter-expended__countries-list {
  margin-left: 10%;
  margin-bottom: 28px;
}

.filter-expended__list {
  margin: 0;
  margin-bottom: 31px;
  margin-left: 9%;
}

@media (min-width: @tablet-width) {
  .filter-expended {
    display: none;
    flex-wrap: wrap;
    padding: 0;
    padding-top: 54px;
    padding-bottom: 20px;
  }

  .filter-expended__alphabet {
    margin-left: 0;
    margin-bottom: 47px;
    align-self: flex-start;
  }

  .filter-expended__countries-list {
    margin-left: auto;
    margin-right: 5.3%;
    margin-top: 2px;
  }

  .filter-expended__button {
    margin-left: 2%;
    margin-right: auto;
  }
}

@media (min-width: @desktop-width) {
  .filter-expended {
    display: flex;
    flex-wrap: wrap;
    padding-bottom: 121px;
  }

  .filter-expended__wrapper-title {
    max-width: 1462px;
    margin-bottom: 94px;
    padding: 0;
    padding-top: 109px;
    padding-right: 5%;

    &--close-filter {
      display: flex;
      justify-content: flex-start;
    }
  }

  .filter-expended__title {
    display: block;
  }

  .filter-expended__continents-list {
    margin-right: 27%;

    &--header-filter {
      display: flex;
    }
  }

  .filter-expended__list {
    display: none;
  }

  .filter-expended__alphabet {
    margin-left: 20px;
    margin-bottom: 32px;
  }

  .filter-expended__button {
    margin-left: 0;
  }
}
