.direction-form__user-information {
  .fieldset-res;
  padding-bottom: 53px;
}

.direction-form__user-plans {
  .fieldset-res;
  margin-top: -20px;

  border-top-left-radius: 10px;
  border-top-right-radius: 10px;
}

.direction-form__add-plans {
  .fieldset-res;
  margin-top: -20px;
}

@media (min-width: @tablet-width) {
  .direction-form__user-information {
    padding-bottom: 75px;
  }

  .direction-form__user-plans {
    margin-top: -21px;
  }
}

@media (min-width: @desktop-width) {
  .direction-form__user-information {
    padding-bottom: 128px;
  }

  .direction-form__user-plans {
    margin-top: -70px;
  }

  .direction-form__add-plans {
    margin-top: -60px;
  }
}
