.wish-list {
  position: relative;
  width: 100%;
}

.wish-list__title {
  .indents-reset;
  position: relative;
  top: 10px;
  width: 45%;
  margin-left: 5%;

  .regular-font;
  font-size: 15px;
  line-height: 18px;

  word-break: break-all;

  text-align: center;
  background-color: @clear-bg;
}

.wish-list__list {
  .list-res;
  padding-top: 26px;

  border-top: 1px solid @available-days;
}

.wish-list__item {
  position: relative;
  width: 57%;
  padding-left: 24.2%;

  .medium-font;
  font-size: 15px;
  line-height: 18px;
  color: @primary-dark-font-color;

  word-break: break-all;

  text-transform: uppercase;

  &:not(:last-of-type) {
    margin-bottom: 10px;
  }

  &::before {
    content: "";
    position: absolute;
    top: -3px;
    left: 26px;
    width: 26px;
    height: 18px;

    background-size: contain;
    background-repeat: no-repeat;
  }

  &--shri-lanka::before {
    background-image: url("../img/flag-sri-lanka.svg");
  }

  &--thailand::before {
    background-image: url("../img/flag-thailand.svg");
  }

  &--seysheli::before {
    background-image: url("../img/flag-seychelles.svg");
  }

  &--belgium::before {
    background-image: url("../img/flag-belgium.svg");
  }

  &--czech::before {
    background-image: url("../img/flag-czech.svg");
  }

  &--usa::before {
    background-image: url("../img/flag-usa.svg");
  }

  &--australia::before {
    background-image: url("../img/flag-australia.svg");
  }

  &--dominican::before {
    background-image: url("../img/flag-dominica.svg");
  }

  &--britan::before {
    background-image: url("../img/flag-united-kingdom.svg");
  }

  &--german::before {
    background-image: url("../img/flag-germany.svg");
    background-size: 32px 26px;
    background-position-y: -4px;
    background-position-x: -2px;
  }
}

@media (min-width: @tablet-width) {
  .wish-list {
    width: 100%;
  }

  .wish-list__title {
    position: relative;
    top: 0;
    left: 2px;
    z-index: 1;
    width: 17%;
    margin: 0;
    margin-bottom: 17px;
    margin-left: 10%;

    font-size: 15px;
    line-height: 18px;
  }

  .wish-list__list {
    position: relative;
    width: 62%;
    padding: 0;
    padding-left: 11.5%;
    display: flex;
    justify-content: flex-start;
    flex-wrap: wrap;

    border-top: none;

    &::before {
      content: "";
      position: absolute;
      top: -27px;
      left: 0;
      width: 136%;
      height: 1px;
      display: block;

      background-color: @light-bg;
    }
  }

  .wish-list__item {
    width: auto;
    padding: 0;

    font-size: 20px;
    line-height: 30px;

    &:not(:last-of-type) {
      margin-right: 17.7%;
      margin-bottom: 0;
    }

    &::before {
      content: "";
      position: absolute;
      top: 0;
      left: -46px;
      width: 35px;
      height: 24px;

      background-size: contain;
    }

    &--german::before {
      background-size: 36px 39px;
      top: -1px;
      left: -49px;
      background-position-y: -7px;
    }
  }
}


@media (min-width: @desktop-width) {
  .wish-list {
    position: absolute;
    right: 0;
    top: 26px;
    width: 30%;
    border-top: none;
  }

  .wish-list__list {
    width: 100%;
    padding: 0;
    margin: 0;
    flex-direction: column;

    &::before {
      content: none;
    }
  }

  .wish-list__title {
    display: none;
  }

  .wish-list__item {
    width: 56%;
    padding: 0;
    padding-left: 22%;

    &:not(:last-of-type) {
      margin-bottom: 6px;
    }

    &::before {
      top: 1px;
      left: 9px;
    }
  }
}
